import * as types from "./JobDescriptionTypes";

const INITIAL_STATE = {
  jobDescriptionData: [],
  isJobDescriptionAdded: false,
  getJobDescriptionData: [],
  isGetJobDescriptionData: false,
  getJobtitleData: [],
  isGetJobtitleData: false,
  isJobDescriptionUpdated: false,
  isJobDescriptionDeleted: false,
  isJobDescriptionMsgSent: false,
  isJobDescriptionByIdData: false,
  getJobDescriptionByIdData: [],
  dateFilterJobDescriptionData: [],
  isDateFilterJobDescriptionData: false,
  updateJobTitle: [],
  isUpdateJobTitle: false,
};

export function JobDescriptionReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case types.CREATE_JOBDESCRIPTION:
      return {
        ...state,
        isJobDescriptionAdded: false,
      };

    case types.CREATE_JOBDESCRIPTION_SUCCESS:
      return {
        ...state,
        isJobDescriptionAdded: true,
        jobDescriptionData: action.payload,
      };

    case types.CREATE_JOBDESCRIPTION_ERROR:
      return {
        ...state,
        isJobDescriptionAdded: false,
      };

    case types.GET_JOBDESCRIPTION:
      return {
        ...state,
        isGetJobDescriptionData: false,
      };

    case types.GET_JOBDESCRIPTION_SUCCESS:
      return {
        ...state,
        getJobDescriptionData: action.payload.data,
        isGetJobDescriptionData: true,
      };

    case types.GET_JOBDESCRIPTION_ERROR:
      return {
        ...state,
        isGetJobDescriptionData: false,
      };
    // job title
    case types.GET_JOBTITLE:
      return {
        ...state,
        isGetJobtitleData: false,
      };

    case types.GET_JOBTITLE_SUCCESS:
      return {
        ...state,
        getJobtitleData: action.payload,
        isGetJobtitleData: true,
      };

    case types.GET_JOBTITLE_ERROR:
      return {
        ...state,
        isGetJobtitleData: false,
      };
    // end of job title

    case types.UPDATE_JOBDESCRIPTION:
      return {
        ...state,
        isJobDescriptionUpdated: false,
      };

    case types.UPDATE_JOBDESCRIPTION_SUCCESS:
      return {
        ...state,
        isJobDescriptionUpdated: true,
      };

    case types.UPDATE_JOBDESCRIPTION_ERROR:
      return {
        ...state,
        isJobDescriptionUpdated: false,
      };

    case types.DELETE_JOBDESCRIPTION:
      return {
        ...state,
        isJobDescriptionDeleted: false,
      };

    case types.DELETE_JOBDESCRIPTION_SUCCESS:
      return {
        ...state,
        isJobDescriptionDeleted: true,
      };

    case types.DELETE_JOBDESCRIPTION_ERROR:
      return {
        ...state,
        isJobDescriptionDeleted: false,
      };

    case types.SEND_MESSAGE_JOBDESCRIPTION:
      return {
        ...state,
        isJobDescriptionMsgSent: false,
      };

    case types.SEND_MESSAGE_JOBDESCRIPTION_SUCCESS:
      return {
        ...state,
        isJobDescriptionMsgSent: true,
      };

    case types.SEND_MESSAGE_JOBDESCRIPTION_ERROR:
      return {
        ...state,
        isJobDescriptionMsgSent: false,
      };

    case types.GET_JOBDESCRIPTION_BY_ID:
      return {
        ...state,
        isJobDescriptionByIdData: false,
        getJobDescriptionByIdData: [],
      };

    case types.GET_JOBDESCRIPTION_BY_ID_SUCCESS:
      return {
        ...state,
        isJobDescriptionByIdData: true,
        getJobDescriptionByIdData: action.payload,
      };

    case types.GET_JOBDESCRIPTION_BY_ID_ERROR:
      return {
        ...state,
        isJobDescriptionByIdData: false,
        getJobDescriptionByIdData: [],
      };

    case types.DELETE_ALL_JOBDESCRIPTION_DATA:
      return {
        ...state,
      };

    case types.DELETE_ALL_JOBDESCRIPTION_DATA_SUCCESS:
      return {
        ...state,
      };

    case types.DELETE_ALL_JOBDESCRIPTION_DATA_ERROR:
      return {
        ...state,
      };

    case types.DATE_FILTER_JOBDESCRIPTION:
      return {
        ...state,
        isDateFilterJobDescriptionData: false,
      };

    case types.DATE_FILTER_JOBDESCRIPTION_SUCCESS:
      return {
        ...state,
        getJobDescriptionData: action.payload.data,
        isDateFilterJobDescriptionData: true,
      };

    case types.DATE_FILTER_JOBDESCRIPTION_ERROR:
      return {
        ...state,
        isDateFilterJobDescriptionData: false,
      };

    case types.SEARCH_JOBDESCRIPTION:
      return {
        ...state,
        isSearchJobDescriptionData: false,
      };

    case types.SEARCH_JOBDESCRIPTION_SUCCESS:
      return {
        ...state,
        getJobDescriptionData: action.payload.data,
        isSearchJobDescriptionData: true,
      };

    case types.SEARCH_JOBDESCRIPTION_ERROR:
      return {
        ...state,
        isSearchJobDescriptionData: false,
      };

    case types.UPDATE_JOB_TITLE:
      return {
        ...state,
        isUpdateJobTitle: false,
      };

    case types.UPDATE_JOB_TITLE_SUCCESS:
      return {
        ...state,
        updateJobTitle: action.payload,
        isUpdateJobTitle: true,
      };

    case types.UPDATE_JOB_TITLE_ERROR:
      return {
        ...state,
        isUpdateJobTitle: false,
      };

    default:
      return state;
  }
}
