import * as types from "./CommonTypes";

const INITIAL_STATE = {
  isjobTitleLoading: false,
  isjobTitleFetched: false,
  jobTitleList: [],
};

export function CommonReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case types.GET_JOBTITLE:
      return {
        ...state,
        isjobTitleLoading: true,
        isjobTitleFetched: false,
        jobTitleList: [],
      };

    case types.GET_JOBTITLE_SUCCESS:
      return {
        ...state,
        isjobTitleLoading: false,
        isjobTitleFetched: true,
        jobTitleList: action.payload,
      };

    case types.GET_JOBTITLE_ERROR:
      return {
        ...state,
        isjobTitleLoading: false,
        isjobTitleFetched: false,
        jobTitleList: [],
      };

    default:
      return state;
  }
}
