import * as types from "./QuestionTypes";

export function addQuestionData(payload) {
  return {
    type: types.CREATE_QUESTION,
    payload: payload,
  };
}
export function addQuestionDataSuccess(payload) {
  return {
    type: types.CREATE_QUESTION_SUCCESS,
    payload: payload,
  };
}
export function addQuestionDataError(error) {
  return {
    type: types.CREATE_QUESTION_ERROR,
    payload: error,
  };
}

