import * as types from "./JobDescriptionTypes";

// Create JD
export function addJobDescriptionData(payload) {
  return {
    type: types.CREATE_JOBDESCRIPTION,
    payload: payload,
  };
}

export function addJobDescriptionDataSuccess(payload) {
  return {
    type: types.CREATE_JOBDESCRIPTION_SUCCESS,
    payload: payload,
  };
}

export function addJobDescriptionDataError(error) {
  return {
    type: types.CREATE_JOBDESCRIPTION_ERROR,
    payload: error,
  };
}

