import * as types from "./Response.Types";

export function addResponseData(payload) {
  return {
    type: types.CREATE_RESPONSE,
    payload: payload,
  };
}
export function addResponseDataSuccess(payload) {
  return {
    type: types.CREATE_RESPONSE_SUCCESS,
    payload: payload,
  };
}
export function addResponseDataError(error) {
  return {
    type: types.CREATE_RESPONSE_ERROR,
    payload: error,
  };
}

