import { call, put } from "redux-saga/effects";
import * as ACTIONS from "./JobDescriptionAction";
import { takeLatest } from "redux-saga/effects";
import * as TYPES from "./JobDescriptionTypes";
import apiJunction from "../../utils/api";
import serverAddress from "../../config";
import { store } from "react-notifications-component";
import history from "../../routes/History";
const candid = localStorage.getItem("id");
export function* getJobDescriptionData(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "get",
      url: action.payload
        ? `${serverAddress}/jd/?page=${action.payload.page}&limit=${
            action.payload.limit
          }${action.payload.title ? "&title=" + action.payload.title : ""}${
            action.payload.startDate
              ? "&startDate=" + action.payload.startDate
              : ""
          }${
            action.payload.endDate ? "&endDate=" + action.payload.endDate : ""
          }`
        : `${serverAddress}/jd/dropdown`,
    });
    if (result) {
      yield put(ACTIONS.getJobDescriptionDataSuccess(result));
    }
  } catch (e) {
    yield put(ACTIONS.getJobDescriptionDataError(e.response));
  }
}
export function* getJobtitleData(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "get",
      url: `${serverAddress}/api/jobtitle/get/${candid}`,
    });
    if (result) {
      yield put(ACTIONS.getJobtitleDataSuccess(result));
    }
  } catch (e) {
    yield put(ACTIONS.getJobtitleDataError(e.response));
  }
}

export function* createJobDescriptionData(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "post",
      url: `${serverAddress}/jd`,
      body: action.payload,
    });
    if (result) {
      yield put(ACTIONS.addJobDescriptionDataSuccess(result));
      store.addNotification({
        title: "Success",
        message: "JD added successfully!!",
        type: "success",
        container: "top-center",
        animationIn: ["animated", "fadeIn"],
        animationOut: ["animated", "fadeOut"],
        dismiss: {
          duration: 2000,
        },
      });
    }
  } catch (e) {
    yield put(ACTIONS.addJobDescriptionDataError(e.response));
    store.addNotification({
      title: "Failed",
      message: "Failed to add JD",
      type: "danger",
      container: "top-center",
      animationIn: ["animated", "fadeIn"],
      animationOut: ["animated", "fadeOut"],
      dismiss: {
        duration: 2000,
      },
    });
  }
}

export function* updatetJobDescriptionData(action) {
  let body;
  body = {
    description: action.payload.description,
    numberOfPositions: action.payload.numberOfPositions,
    startDate: action.payload.startDate,
    endDate: action.payload.endDate,
    // noQuestion: action.payload.noQuestion,
    // timer: action.payload.timer,
    // questions: action.payload.questions,
    minExp: action.payload.minExp,
    maxExp: action.payload.maxExp,
    status: action.payload.status,
    skills: action.payload.skills,
    title: action.payload.title,
  };

  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "patch",
      url: `${serverAddress}/jd/${action.payload.id}`,
      body: body,
    });
    if (result && result.status == 200) {
      yield put(ACTIONS.updateJobDescriptionDataSuccess(result));
      store.addNotification({
        title: "Success",
        message: "Data Updated successfully",
        type: "success",
        container: "top-center",
        animationIn: ["animated", "fadeIn"],
        animationOut: ["animated", "fadeOut"],
        dismiss: {
          duration: 2000,
        },
      });
    }
  } catch (e) {
    yield put(ACTIONS.updateJobDescriptionDataError(e.response));
    store.addNotification({
      title: "Error",
      message: "Data Not Updated",
      type: "danger",
      container: "top-center",
      animationIn: ["animated", "fadeIn"],
      animationOut: ["animated", "fadeOut"],
      dismiss: {
        duration: 2000,
      },
    });
    // setTimeout(function () {
    //   history.push("/jd");
    // }, 3000);
  }
}

