export function* updateJobTitle(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "patch",
      url: `${serverAddress}/api/Jobtitle/update/${action.payload.jTId}`,
      body: action.payload.data,
    });
    if (result) {
      yield put(ACTIONS.updateJobTitleSuccess(result));
    }
  } catch (e) {
    yield put(ACTIONS.updateJobTitleError(e.response));
  }
}

export function* JobDescriptionSaga() {
  yield takeLatest(TYPES.DATE_FILTER_JOBDESCRIPTION, dateFilterJd);
  yield takeLatest(TYPES.GET_JOBDESCRIPTION, getJobDescriptionData);
  yield takeLatest(TYPES.GET_JOBTITLE, getJobtitleData);
  yield takeLatest(TYPES.CREATE_JOBDESCRIPTION, createJobDescriptionData);
  yield takeLatest(TYPES.UPDATE_JOBDESCRIPTION, updatetJobDescriptionData);
  yield takeLatest(TYPES.DELETE_JOBDESCRIPTION, deleteJobDescriptionData);
  yield takeLatest(TYPES.SEND_MESSAGE_JOBDESCRIPTION, sendMsgJobDescription);
  yield takeLatest(TYPES.GET_JOBDESCRIPTION_BY_ID, getJobDescriptionByIdData);
  yield takeLatest(TYPES.DELETE_ALL_JOBDESCRIPTION_DATA, closeJobDescription);
  yield takeLatest(TYPES.SEARCH_JOBDESCRIPTION, searchJobDescriptionData);
  yield takeLatest(TYPES.UPDATE_JOB_TITLE, updateJobTitle);
}
