import * as types from "./Response.Types";

const INITIAL_STATE = {
  ResponseData: [],
  isResponseAdded: false,
  searchResponseData: [],
  isSearchResponseData: false,
  getResponseData: [],
  isGetResponseData: false,
  isdeleteResponseData: false,
  isResponseByNameData: false,
  getResponseByNameData: [],
  isResponseByIdData: false,
  getResponseByIdData: [],
  isResponseUpdated: false,
  dateFilterData: [],
  isDateFilterData: false,
  updatedcanddata: [],
  flag: 0,
  isAddCommentLoading: false,
  commentAdded: false,
  isAddCommentError: false,
  isUpdateCommentLoading: false,
  commentUpdated: false,
  isUpdateCommentError: false,
  shareCreate: [],
  isShareCreate: false,
  shareGet: [],
  isShareGet: false,
  shareEmail: [],
  isShareEmail: false,
  shareCreateMulti: [],
  isShareCreateMulti: false,
  shareEmailMulti: [],
  isShareEmailMulti: false,
  shareGetMulti: [],
  isShareGetMulti: false,
  updateMultiResponse: [],
  isUpdateMultiResponse: false,
  codingRes: [],
  isCodingRes: false,
  updateCodingScore: [],
  isUpdateScore: false,
};

export function responseReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case types.CREATE_RESPONSE:
      return {
        ...state,
        isResponseAdded: false,
      };

    case types.CREATE_RESPONSE_SUCCESS:
      return {
        ...state,
        isResponseAdded: true,
        ResponseData: action.payload,
      };

    case types.CREATE_RESPONSE_ERROR:
      return {
        ...state,
        isResponseAdded: false,
      };

    case types.GET_RESPONSE:
      return {
        ...state,
        isLoading: true,
        isGetResponseData: false,
      };

    case types.GET_RESPONSE_SUCCESS:
      return {
        ...state,
        isLoading: false,
        getResponseData: action.payload.data,
        isGetResponseData: true,
      };

    case types.GET_RESPONSE_ERROR:
      return {
        ...state,
        isLoading: false,
        isGetResponseData: false,
      };

    case types.SEARCH_RESPONSE:
      return {
        ...state,
        isSearchResponseData: false,
      };

    case types.SEARCH_RESPONSE_SUCCESS:
      return {
        ...state,
        isSearchResponseData: true,
        getResponseData: action.payload.data,
      };

    case types.SEARCH_RESPONSE_ERROR:
      return {
        ...state,
        isSearchResponseData: false,
      };

    case types.DELETE_RESPONSE:
      return {
        ...state,
        isdeleteResponseData: false,
      };

    case types.DELETE_RESPONSE_SUCCESS:
      return {
        ...state,
        flag: 1,
        deleteResponseData: action.payload,
        isdeleteResponseData: true,
      };

    case types.DELETE_RESPONSE_ERROR:
      return {
        ...state,
        isdeleteResponseData: false,
      };

    case types.GET_RESPONSE_BY_ID:
      return {
        ...state,
        isResponseByIdData: false,
        getResponseByIdData: [],
      };

    case types.GET_RESPONSE_BY_ID_SUCCESS:
      return {
        ...state,
        isResponseByIdData: true,
        getResponseByIdData: action.payload,
      };

    case types.GET_RESPONSE_BY_ID_ERROR:
      return {
        ...state,
        isResponseByIdData: false,
        getResponseByIdData: [],
      };
    case types.UPDATE_RESPONSE:
      return {
        ...state,
        isResponseUpdated: false,
        updatedcanddata: [],
      };

    case types.UPDATE_RESPONSE_SUCCESS:
      return {
        ...state,
        isResponseUpdated: true,
        updatedcanddata: action.payload,
      };

    case types.UPDATE_RESPONSE_ERROR:
      return {
        ...state,
        isResponseUpdated: false,
        updatedcanddata: [],
      };

    case types.FILTER_BY_DATE:
      return {
        ...state,
        isDateFilterData: false,
      };

    case types.FILTER_BY_DATE_SUCCESS:
      return {
        ...state,
        getResponseData: action.payload.data,
        isDateFilterData: true,
      };

    case types.FILTER_BY_DATE_ERROR:
      return {
        ...state,
        isDateFilterData: false,
      };

    case types.ADD_COMMENT:
      return {
        ...state,
        isAddCommentLoading: true,
        commentAdded: false,
        isAddCommentError: false,
      };

    case types.ADD_COMMENT_SUCCESS:
      return {
        ...state,
        isAddCommentLoading: false,
        commentAdded: true,
        isAddCommentError: false,
      };

    case types.ADD_COMMENT_ERROR:
      return {
        ...state,
        isAddCommentLoading: false,
        commentAdded: false,
        isAddCommentError: true,
      };

    case types.UPDATE_COMMENT:
      return {
        ...state,
        isUpdateCommentLoading: true,
        commentUpdated: false,
        isUpdateCommentError: false,
      };

    case types.UPDATE_COMMENT_SUCCESS:
      return {
        ...state,
        isUpdateCommentLoading: false,
        commentUpdated: true,
        isUpdateCommentError: false,
      };

    case types.UPDATE_COMMENT_ERROR:
      return {
        ...state,
        isUpdateCommentLoading: false,
        commentUpdated: false,
        isUpdateCommentError: true,
      };

    case types.SHARE_CREATE:
      return {
        ...state,
        isShareCreate: false,
        shareCreate: [],
      };

    case types.SHARE_CREATE_SUCCESS:
      return {
        ...state,
        isShareCreate: true,
        shareCreate: action.payload,
      };

    case types.SHARE_CREATE_ERROR:
      return {
        ...state,
        isShareCreate: false,
        shareCreate: [],
      };

    case types.SHARE_GET:
      return {
        ...state,
        isShareGet: false,
        shareGet: [],
      };

    case types.SHARE_GET_SUCCESS:
      return {
        ...state,
        isShareGet: true,
        shareGet: action.payload,
      };

    case types.SHARE_GET_ERROR:
      return {
        ...state,
        isShareGet: false,
        shareGet: [],
      };

    case types.SHARE_MAIL:
      return {
        ...state,
        isShareEmail: false,
        shareEmail: [],
      };

    case types.SHARE_MAIL_SUCCESS:
      return {
        ...state,
        isShareEmail: true,
        shareEmail: action.payload,
      };

    case types.SHARE_MAIL_ERROR:
      return {
        ...state,
        isShareEmail: false,
        shareEmail: [],
      };
    case types.SHARE_MULTI_CREATE:
      return {
        ...state,
        isShareCreateMulti: false,
        shareCreateMulti: [],
      };

    case types.SHARE_MULTI_CREATE_SUCCESS:
      return {
        ...state,
        isShareCreateMulti: true,
        shareCreateMulti: action.payload,
      };

    case types.SHARE_MULTI_CREATE_ERROR:
      return {
        ...state,
        isShareCreateMulti: false,
        shareCreateMulti: [],
      };

    case types.SHARE_MULTI_MAIL:
      return {
        ...state,
        isShareEmailMulti: false,
        shareEmailMulti: [],
      };

    case types.SHARE_MULTI_MAIL_SUCCESS:
      return {
        ...state,
        isShareEmailMulti: true,
        shareEmailMulti: action.payload,
      };

    case types.SHARE_MULTI_MAIL_ERROR:
      return {
        ...state,
        isShareEmailMulti: false,
        shareEmailMulti: [],
      };

    case types.SHARE_GET_MULTI:
      return {
        ...state,
        isShareGetMulti: false,
        shareGetMulti: [],
      };

    case types.SHARE_GET_MULTI_SUCCESS:
      return {
        ...state,
        isShareGetMulti: true,
        shareGetMulti: action.payload,
      };

    case types.SHARE_GET_MULTI_ERROR:
      return {
        ...state,
        isShareGetMulti: false,
        shareGetMulti: [],
      };

    case types.UPDATE_MULTI_RESPONSE:
      return {
        ...state,
        isUpdateMultiResponse: false,
        updateMultiResponse: [],
      };

    case types.UPDATE_MULTI_RESPONSE_SUCCESS:
      return {
        ...state,
        isUpdateMultiResponse: true,
        updateMultiResponse: action.payload,
      };

    case types.UPDATE_MULTI_RESPONSE_ERROR:
      return {
        ...state,
        isUpdateMultiResponse: false,
        updateMultiResponse: [],
      };

    case types.GET_CODING_RESPONSE:
      return {
        ...state,
        isCodingRes: false,
        codingRes: [],
      };

    case types.GET_CODING_RESPONSE_SUCCESS:
      return {
        ...state,
        isCodingRes: true,
        codingRes: action.payload,
      };

    case types.GET_CODING_RESPONSE_ERROR:
      return {
        ...state,
        isCodingRes: false,
        codingRes: [],
      };

    case types.UPDATE_CODING_SCORE:
      return {
        ...state,
        isUpdateScore: false,
        updateCodingScore: [],
      };

    case types.UPDATE_CODING_SCORE_SUCCESS:
      return {
        ...state,
        isUpdateScore: true,
        updateCodingScore: action.payload,
      };

    case types.UPDATE_CODING_SCORE_ERROR:
      return {
        ...state,
        isUpdateScore: false,
        updateCodingScore: [],
      };
    default:
      return state;
  }
}
