import * as types from "./CommonTypes";

export function getJobTitle() {
  return {
    type: types.GET_JOBTITLE,
  };
}
export function getJobTitleSuccess(payload) {
  return {
    type: types.GET_JOBTITLE_SUCCESS,
    payload: payload,
  };
}
export function getJobTitleError(error) {
  return {
    type: types.GET_JOBTITLE_ERROR,
    payload: error,
  };
}
