import * as types from "./SendTypes";

const INITIAL_STATE = {
  sendData: [],
  isSendAdded: false,
  getSendData: [],
  isGetSendData: false,
  isdeleteSendData: false,
  isSendByNameData: false,
  getSendByNameData: [],
};

export function SendReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case types.CREATE_SENDMESSAGE:
      return {
        ...state,
        isSendAdded: false,
      };

    case types.CREATE_SENDMESSAGE_SUCCESS:
      return {
        ...state,
        isSendAdded: true,
        sendData: action.payload,
      };

    case types.CREATE_SENDMESSAGE_ERROR:
      return {
        ...state,
        isSendAdded: false,
      };

    default:
      return state;
  }
}
