import * as types from "./CandidateTypes";

export function addCandidateData(payload) {
  return {
    type: types.CREATE_CANDIDATE,
    payload: payload,
  };
}

export function addCandidateDataSuccess(payload) {
  return {
    type: types.CREATE_CANDIDATE_SUCCESS,
    payload: payload,
  };
}

export function addCandidateDataError(error) {
  return {
    type: types.CREATE_CANDIDATE_ERROR,
    payload: error,
  };
}

export function getCandidateData(payload) {
  return {
    type: types.GET_CANDIDATE,
    payload: payload,
  };
}

export function getCandidateDataSuccess(payload) {
  return {
    type: types.GET_CANDIDATE_SUCCESS,
    payload: payload,
  };
}

export function getCandidateDataError(error) {
  return {
    type: types.GET_CANDIDATE_ERROR,
    payload: error,
  };
}

