import * as types from "./SendTypes";

export function addSendData(payload) {
  return {
    type: types.CREATE_SENDMESSAGE,
    payload: payload,
  };
}
export function addSendDataSuccess(payload) {
  return {
    type: types.CREATE_SENDMESSAGE_SUCCESS,
    payload: payload,
  };
}
export function addSendDataError(error) {
  return {
    type: types.CREATE_SENDMESSAGE_ERROR,
    payload: error,
  };
}
