import * as types from "./resumesourceTypes";

export function addCandidateData(payload) {
  return {
    type: types.CREATE_CANDIDATE,
    payload: payload,
  };
}
export function addCandidateDataSuccess(payload) {
  return {
    type: types.CREATE_CANDIDATE_SUCCESS,
    payload: payload,
  };
}
export function addCandidateDataError(error) {
  return {
    type: types.CREATE_CANDIDATE_ERROR,
    payload: error,
  };
}

