import * as types from "./QuestionCreateTypes";

export function addQuestionData(payload) {
  return {
    type: types.CREATE_NEWQUESTION,
    payload: payload,
  };
}
export function addQuestionDataSuccess(payload) {
  return {
    type: types.CREATE_NEWQUESTION_SUCCESS,
    payload: payload,
  };
}
export function addQuestionDataError(error) {
  return {
    type: types.CREATE_NEWQUESTION_ERROR,
    payload: error,
  };
}

