import React, { Suspense } from "react";
import { Router, BrowserRouter, Switch } from "react-router-dom";
import history from "./History";
import * as LazyComponent from "../utils/LazyLoaded";
import Loader from "../components/Loader/Loader";

const Routes = ({}) => {
  return (
    <Suspense fallback={<Loader />}>
      <BrowserRouter basename={process.env.REACT_APP_BASEURL} history={history}>
        <Switch>
          <LazyComponent.Login path="/login" exact />
          <LazyComponent.Dummy path="/dummy" exact />
          <LazyComponent.UuiVideoModal
            path="/response/share-interview/:id"
            exact
          />
          <LazyComponent.UuidMultipleResponse
            path="/response/share-multi-interview/:id"
            exact
          />
          <LazyComponent.Resume_Sourcing path="/resumeSourcing" exact />
          <LazyComponent.Home path="/home" exact />
          <LazyComponent.Dashboard path="/dashboard" exact />
          <LazyComponent.UserManagement path="/user-management" exact />
          <LazyComponent.Settings path="/settings" exact />
          <LazyComponent.Response path="/response" exact />
          <LazyComponent.Jd path="/jd" exact />
          <LazyComponent.AddJobtitle path="/master/addjobtitle" exact />
          <LazyComponent.FollowUp path="/followUp" exact />
          <LazyComponent.AllSchedule path="/schedule-list" exact />
          <LazyComponent.Schedule path="/Schedule" exact />
          <LazyComponent.Questions path="/addquestiondata" exact />
          <LazyComponent.Verbal path="/verbal" exact />
          <LazyComponent.MCQ path="/MCQ" exact />
          <LazyComponent.Coding path="/coding" exact />
          <LazyComponent.ListOfAssessment path="/assessmentList" exact />
          <LazyComponent.Response path="/shortlist" exact />
          <LazyComponent.Response path="/selected" exact />
          <LazyComponent.Timeline path="/timeline" exact />
          <LazyComponent.JdList path="/jd-list" exact />
          <LazyComponent.AddJD path="/AddJD" exact />
          <LazyComponent.jddata path="/jddata/:id" exact />
          <LazyComponent.CandidatesList path="/showCandidates" exact />
          <LazyComponent.AssessmentStep1 path="/assessment/step1" exact />
          <LazyComponent.TypeOfQuestions
            path="/assessment/step2/create-assignment/:id"
            exact
          />
          <LazyComponent.Summary
            path="/assessment/step2/assignment-flow/:id"
            exact
          />
          <LazyComponent.Preview path="/assessment/step2/preview/:id" exact />
          <LazyComponent.Publish path="/assessment/step2/publish/:id" exact />
          <LazyComponent.Invites path="/invites/:id" exact />
          <LazyComponent.InviteHistory path="/history/:id" exact />
          <LazyComponent.ResponseSingle path="/response/single/:id" exact />
          <LazyComponent.ResponseCode path="/response/code/:id" exact />
          <LazyComponent.NotFound path="/*" />
          {/* <PrivateRoute component={LazyComponent.Home} path="/:lang/" exact /> */}
          {/* <Redirect from="**" to={`/${lang}/`} exact /> */}
        </Switch>
      </BrowserRouter>
    </Suspense>
  );
};

export default Routes;
