import * as types from "./scheduleTypes";

const INITIAL_STATE = {
  isScheduleCreated: false,
  isGetScheduleData: false,
  isScheduleUpdated: false,
  getScheduleData: [],
  getScheduleDataById: [],
  isGetScheduleByIdData: false,
  invites: [],
  invitesDataArrived: false,
  scheduleData: [],
  isScheduleData: false,
  inviteHistory: [],
  isInviteHistory: false,
  isMessagePosted: false,
  isEmailPosted: false,
  templates: [],
  templatesDataArrived: false,
  isTemplateMessagePosted: false,
  whatsAppSeen: [],
  isWhatsappSeen: false,
};

export function ScheduleReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case types.CREATE_SCHEDULE:
      return {
        ...state,
        isScheduleCreated: false,
      };

    case types.CREATE_SCHEDULE_SUCCESS:
      return {
        ...state,
        isScheduleCreated: true,
      };

    case types.CREATE_SCHEDULE_ERROR:
      return {
        ...state,
        isScheduleCreated: false,
      };
    case types.GET_SCHEDULE:
      return {
        ...state,
        isGetScheduleData: false,
      };

    case types.GET_SCHEDULE_SUCCESS:
      return {
        ...state,
        getScheduleData: action.payload,
        isGetScheduleData: true,
      };

    case types.GET_SCHEDULE_ERROR:
      return {
        ...state,
        isGetScheduleData: false,
      };
    case types.GET_SCHEDULE_BY_ID:
      return {
        ...state,
        isGetScheduleByIdData: false,
      };

    case types.GET_SCHEDULE_BY_ID_SUCCESS:
      return {
        ...state,
        getScheduleDataById: action.payload,
        isGetScheduleByIdData: true,
      };

    case types.GET_SCHEDULE_BY_ID_ERROR:
      return {
        ...state,
        isGetScheduleByIdData: false,
      };
    case types.UPDATE_SCHEDULE:
      return {
        ...state,
        isScheduleUpdated: false,
      };

    case types.UPDATE_SCHEDULE_SUCCESS:
      return {
        ...state,
        isScheduleUpdated: true,
      };

    case types.UPDATE_SCHEDULE_ERROR:
      return {
        ...state,
        isScheduleUpdated: false,
      };

    case types.GET_INVITES:
      return {
        ...state,
        invitesDataArrived: false,
      };

    case types.GET_INVITES_SUCCESS:
      return {
        ...state,
        invites: action.payload,
        invitesDataArrived: true,
      };

    case types.GET_INVITES_ERROR:
      return {
        ...state,
        invitesDataArrived: false,
      };

    case types.GET_SCHEDULE_WITH_RESPONSE:
      return {
        ...state,
        scheduleData: [],
        isScheduleData: false,
      };

    case types.GET_SCHEDULE_WITH_RESPONSE_SUCCESS:
      return {
        ...state,
        scheduleData: action.payload,
        isScheduleData: true,
      };

    case types.GET_SCHEDULE_WITH_RESPONSE_ERROR:
      return {
        ...state,
        scheduleData: [],
        isScheduleData: false,
      };

    case types.INVITE_HISTORY:
      return {
        ...state,
        inviteHistory: [],
        isInviteHistory: false,
      };

    case types.INVITE_HISTORY_SUCCESS:
      return {
        ...state,
        inviteHistory: action.payload,
        isInviteHistory: true,
      };

    case types.INVITE_HISTORY_ERROR:
      return {
        ...state,
        inviteHistory: [],
        isInviteHistory: false,
      };
    case types.CREATE_MESSAGE:
      return {
        ...state,
        isMessagePosted: false,
      };

    case types.CREATE_MESSAGE_SUCCESS:
      return {
        ...state,
        isMessagePosted: true,
      };

    case types.CREATE_MESSAGE_ERROR:
      return {
        ...state,
        isMessagePosted: false,
      };
    case types.CREATE_EMAIL:
      return {
        ...state,
        isEmailPosted: false,
      };

    case types.CREATE_EMAIL_SUCCESS:
      return {
        ...state,
        isEmailPosted: true,
      };

    case types.CREATE_EMAIL_ERROR:
      return {
        ...state,
        isEmailPosted: false,
      };
    case types.GET_TEMPLATES:
      return {
        ...state,
        templatesDataArrived: false,
      };

    case types.GET_TEMPLATES_SUCCESS:
      return {
        ...state,
        templates: action.payload,
        templatesDataArrived: true,
      };

    case types.GET_TEMPLATES_ERROR:
      return {
        ...state,
        templatesDataArrived: false,
      };

    case types.SEND_TEMPLATE_MESSAGE:
      return {
        ...state,
        isTemplateMessagePosted: false,
      };

    case types.SEND_TEMPLATE_MESSAGE_SUCCESS:
      return {
        ...state,
        isTemplateMessagePosted: true,
      };

    case types.SEND_TEMPLATE_MESSAGE_ERROR:
      return {
        ...state,
        isTemplateMessagePosted: false,
      };

    case types.WHATSAPP_SEEN:
      return {
        ...state,
        whatsAppSeen: [],
      };

    case types.WHATSAPP_SEEN_SUCCESS:
      return {
        ...state,
        whatsAppSeen: action.payload,
        isWhatsappSeen: true,
      };

    case types.WHATSAPP_SEEN_ERROR:
      return {
        ...state,
        isWhatsappSeen: false,
      };

    default:
      return state;
  }
}
