import React from "react";
import { Button, makeStyles } from "@material-ui/core";
//  To get the hover effect do not pass the class name else send default classname as main
const ButtonNew = (props) => {
  const useStyles = makeStyles((theme) => ({
    root: {
      "&:hover": {
        backgroundColor: "#04724D !important",
        color: "#fff !important",
        opacity: "0.9",
      },
    },
  }));

  const classes = useStyles();
  let icon = null;
  if (props.endIcon) {
    icon = (
      <img
        src={props.endIcon}
        style={{
          marginLeft: props.iconMarginLeft ? props.iconMarginLeft : "10px",
        }}
      />
    );
  }

  let icon2 = null;
  if (props.startIcon) {
    icon2 = (
      <img
        src={props.startIcon}
        style={{
          marginRight: props.iconMarginRight ? props.iconMarginRight : "10px",
          height:'12px',
          width:'12px'
        }}
      />
    );
  }

  return (
    <Button
      style={{
        fontFamily: 'Poppins,sans-serif',
        backgroundColor: props.active
          ? "#04724d"
          : props.backgroundColor
          ? props.backgroundColor
          : props.disabled ? "#CCCCCC"  : "#04724d",
        fontSize: props.fontSize ? props.fontSize : "",
        fontWeight: props.fontWeight ? props.fontWeight : "",
        color: props.active ? "#ffffff" : props.color ? props.color : "#ffffff",
        width: props.width ? props.width : "180px",
        height: props.height ? props.height : "34px",
        border: props.border ? props.border : "",
        borderRadius: props.borderRadius ? props.borderRadius : "4px",
        textAlign: "center",
        textTransform: "capitalize",
        margin: props.margin ? props.margin : "2px",
        marginTop: props.marginTop ? props.marginTop : "",
        padding: "10px",
        borderLeft: props.borderLeft ? props.borderLeft : "",
        outline: "none",
        minWidth: props.minWidth ? props.minWidth : "30px",
      }}
      onClick={props.onClick}
      className={props.className ? props.className : classes.root}
      disabled={props.disabled ? props.disabled : false}
      endIcon={icon}
      startIcon={icon2}
    >
      {props.children}
    </Button>
  );
};

export default ButtonNew;
