import React from "react";
import { NavLink, Link } from "react-router-dom";
import MenuButton from "../assets/images/menu-button.svg";
import "./sideNav.scss";
import responsedropdown from "../assets/images/responseDropdownIcon.svg";
import ArrowDropUpIcon from "@material-ui/icons/ArrowDropUp";

const superAdminLinks = [
  {
    main: "Dashboard",
    subLinks: [
      {
        link: "/dashboard",
        heading: "Dashboard",
        exact: true,
        class: "dashboard",
      },
    ],
  },

  {
    main: "JD List",
    subLinks: [
      {
        link: "/jd",
        heading: "JD List",
        exact: true,
        class: "jdList",
      },
    ],
  },
  {
    main: "Assessment",
    subLinks: [
      {
        link: "/assessment",
        heading: "Assessment",
        exact: true,
        class: "assessment",
        subMenus: [
          {
            link: "/assessment/step1",
            heading: "Create Assessment",
            exact: true,
            class: "toggle-off",
            subMenus: [],
          },
          {
            link: "/assessmentList",
            heading: "List of Assessment",
            exact: true,
            class: "toggle-off",
            subMenus: [],
          },
          {
            link: "/questions",
            heading: "Questions",
            exact: true,
            class: "questions",
            subMenus: [
              {
                main: "Verbal",
                link: "/verbal",
                heading: "Verbal",
                exact: true,
                class: "toggle-off",
              },
              {
                main: "MCQ",
                link: "/MCQ",
                heading: "MCQ",
                exact: true,
                class: "toggle-off",
              },
              {
                main: "Coding",
                link: "/coding",
                heading: "Coding",
                exact: true,
                class: "toggle-off",
              },
            ],
          },
        ],
      },
    ],
  },
  {
    main: "Profile Sourcing",
    subLinks: [
      {
        link: "/resumeSourcing",
        heading: "Profile Sourcing",
        exact: true,
        class: "resumeSourcing",
      },
    ],
  },

  {
    main: "Schedule",
    subLinks: [
      {
        link: "/Schedule",
        heading: "Schedule",
        exact: true,
        class: "schedule",
        subMenus: [
          {
            link: "/Schedule",
            heading: "Create Schedule",
            exact: true,
            class: "toggle-off",
            subMenus: [],
          },
          {
            link: "/schedule-list",
            heading: "Schedule List",
            exact: true,
            class: "toggle-off",
            subMenus: [],
          },
        ],
      },
    ],
  },

  {
    main: "Response",
    subLinks: [
      {
        link: "/response",
        heading: "Response",
        exact: true,
        class: "response",
        subMenus: [
          {
            link: "/response",
            heading: "All",
            exact: true,
            class: "toggle-off",
            subMenus: [],
          },
          {
            link: "/shortlist",
            query: "?status=SHORTLISTED",
            heading: "Shortlist",
            exact: true,
            class: "toggle-off",
            subMenus: [],
          },
          {
            link: "/selected",
            query: "?status=OFFER_RELEASED",
            heading: "Selected",
            exact: true,
            class: "toggle-off",
            subMenus: [],
          },
        ],
      },
    ],
  },

  {
    main: "Settings",
    subLinks: [
      {
        link: "/Settings",
        heading: "Settings",
        exact: true,
        class: "userManagement",
      },
    ],
  },
];

class SideNav extends React.Component {
  state = {
    subLinksTrue: false,
    showDropdown: false,
    showDroptitle: false,
    dropdownName: "",
    questionDropdown: false,
    hideDropdown: {
      name: "",
      isShow: false,
    },
  };

  componentDidMount() {
    const getSideNav = JSON.parse(localStorage.getItem("sideNav"));
    const ques = JSON.parse(localStorage.getItem("quesSideNav"));
    if (getSideNav) {
      this.setState({
        hideDropdown: getSideNav,
      });
    }
    if (ques) {
      this.setState(ques);
    }
  }
  NavComponent = (submenu) => {
    return submenu.heading === "Questions" ? (
      <div
        className={
          this.props.navClass ? "small-response-div" : "large-response-div"
        }
      >
        <div
          className={submenu.class}
          style={{
            display: "flex",
            color: "#fff",
            fontSize: "13px",
          }}
          onClick={() => this.handleResponseMenu(submenu.heading)}
        >
          <i>{submenu.icon}</i>
          <span style={{ marginLeft: "10px" }}>{submenu.heading} </span>
        </div>
        {!this.props.navClass ? (
          <>
            <span
              className="dropdown-icon"
              onClick={() => this.handleResponseMenu(submenu.heading)}
            >
              {this.state.questionDropdown ? (
                <ArrowDropUpIcon
                  style={{
                    width: "20px",
                    height: "20px",
                    color: "#fff",
                  }}
                  className="response-dropdown"
                />
              ) : (
                <img
                  src={responsedropdown}
                  className="response-dropdown"
                  alt="responsedrop"
                />
              )}
            </span>
          </>
        ) : null}
      </div>
    ) : (
      <React.Fragment>
        <div
          id="topBorder"
          style={{
            borderTopWidth: "thin",
            borderTopColor: "#8A8A8A",
            opacity: "25%",
          }}
        ></div>
        <NavLink
          to={{
            pathname: submenu.link,
            search: submenu.query ? submenu.query : "",
            state: {
              tabValue:
                submenu.heading == "Shortlist"
                  ? "shortlisted"
                  : submenu.heading == "Selected"
                  ? "offer_released"
                  : submenu.heading == "Schedule List"
                  ? "Schedule_list"
                  : null,
            },
          }}
          title={submenu.heading}
          className={submenu.class}
        >
          <i>{submenu.icon}</i>
          {!this.props.navClass ? <span>{submenu.heading}</span> : null}
        </NavLink>
      </React.Fragment>
    );
  };

  handleResponseMenu = (name) => {
    const { questionDropdown } = this.state;

    if (name === "Questions") {
      this.setState({
        questionDropdown: !questionDropdown,
        dropdownName: name,
      });
      localStorage.setItem(
        "quesSideNav",
        JSON.stringify({
          questionDropdown: !questionDropdown,
          dropdownName: name,
        })
      );
    } else {
      if (
        this.state.hideDropdown.name === name &&
        this.state.hideDropdown.isShow
      ) {
        /**based on sidenav name,storing the values */
        this.setState({
          hideDropdown: {
            name,
            isShow: false,
          },
        });
        localStorage.setItem(
          "sideNav",
          JSON.stringify({
            name,
            isShow: false,
          })
        );
      } else {
        this.setState({
          hideDropdown: {
            name,
            isShow: true,
          },
        });
        localStorage.setItem(
          "sideNav",
          JSON.stringify({
            name,
            isShow: true,
          })
        );
      }
    }
  };
  handleResponsetitle = () => {
    const { showDroptitle } = this.state;
    this.setState({
      showDroptitle: !showDroptitle,
    });
  };
  handlePathName = (url, heading, sublink) => {
    const pathName = window.location.pathname;
    const search = window.location.search;

    // if (heading === "Shortlist" || heading === "Selected") {
    //   const path = pathName + search;
    //   return path.includes(url) && sublink && sublink.heading === heading;
    //   //   // } else if (
    //   //   //   heading === "All" &&
    //   //   //   (search !== "?status=OFFER_ACCEPTED" || search !== "?status=SHORTLISTED")
    //   //   // ) {
    //   //   //   return pathName.includes(url) && sublink && sublink.heading === heading;
    // } else {
    return pathName.includes(url) && sublink && sublink.heading === heading;
    // }
  };
  render() {
    let sidebarValues = superAdminLinks;
    const { questionDropdown } = this.state;
    return (
      <nav id={this.props.navClass ? "sidebar" : "sidebar-toggle"}>
        <>
          <div style={{ marginLeft: "30px" }}>
            <img
              className="hamstyle"
              src={MenuButton}
              alt="menu-button"
              onClick={() => this.props.toggleClass()}
            />
          </div>

          <ul className="list-unstyled">
            {sidebarValues.map((row, index) => {
              return (
                <React.Fragment key={("mainLink-", row.main)}>
                  {row.subLinks.map((sublink, subIndex) => {
                    /**to display dropdown */
                    if (
                      sublink.heading === "Response" ||
                      sublink.heading === "Assessment" ||
                      sublink.heading === "Schedule"
                    ) {
                      return (
                        <div key={subIndex}>
                          <li key={("sublink-", sublink.heading)}>
                            <div
                              id="topBorder"
                              style={{
                                borderTopWidth: "thin",
                                borderTopColor: "#8A8A8A",
                                opacity: "25%",
                              }}
                            ></div>
                            <div
                              className={
                                this.props.navClass
                                  ? "small-response-div"
                                  : "large-response-div"
                              }
                            >
                              <div
                                className={sublink.class}
                                style={{
                                  display: "flex",
                                  color: "#fff",
                                  fontSize: "13px",
                                }}
                                onClick={() =>
                                  this.handleResponseMenu(sublink.heading)
                                }
                              >
                                <i>{sublink.icon}</i>
                                <span style={{ marginLeft: "10px" }}>
                                  {sublink.heading}{" "}
                                </span>
                              </div>
                              {!this.props.navClass ? (
                                <>
                                  <span
                                    className="dropdown-icon"
                                    onClick={() =>
                                      this.handleResponseMenu(sublink.heading)
                                    }
                                  >
                                    {this.state.hideDropdown.name ===
                                      sublink.heading &&
                                    this.state.hideDropdown.isShow ? (
                                      <ArrowDropUpIcon
                                        style={{
                                          width: "20px",
                                          height: "20px",
                                          color: "#fff",
                                        }}
                                        className="response-dropdown"
                                      />
                                    ) : (
                                      <img
                                        src={responsedropdown}
                                        className="response-dropdown"
                                        alt="responsedrop"
                                      />
                                    )}
                                  </span>
                                </>
                              ) : null}
                            </div>
                            <div>
                              {this.state.hideDropdown.name ===
                                sublink.heading &&
                                this.state.hideDropdown.isShow &&
                                sublink &&
                                sublink.subMenus.map((submenu) => (
                                  <li
                                    key={("sublink-", submenu.heading)}
                                    className={
                                      this.handlePathName(
                                        "/shortlist",
                                        "Shortlist",
                                        submenu
                                      ) ||
                                      this.handlePathName(
                                        "/selected",
                                        "Selected",
                                        submenu
                                      ) ||
                                      this.handlePathName(
                                        "/timeline",
                                        "Timeline",
                                        submenu
                                      ) ||
                                      this.handlePathName(
                                        "/response",
                                        "All",
                                        submenu
                                      ) ||
                                      this.handlePathName(
                                        "/assessment/step1",
                                        "Create Assessment",
                                        submenu
                                      ) ||
                                      // window.location.pathname.includes(
                                      //   "/step2"
                                      // )
                                      //  ||
                                      this.handlePathName(
                                        "/assessmentList",
                                        "List of Assessment",
                                        submenu
                                      ) ||
                                      this.handlePathName(
                                        "/assessment/step1",
                                        "Create Assessment"
                                      ) ||
                                      this.handlePathName(
                                        "/schedule-list",
                                        "Schedule List",
                                        submenu
                                      ) ||
                                      this.handlePathName(
                                        "/Schedule",
                                        "Create Schedule",
                                        submenu
                                      )
                                        ? "large-user-selected"
                                        : null
                                    }
                                  >
                                    {this.NavComponent(submenu)}
                                    {questionDropdown &&
                                      submenu.subMenus.length > 0 &&
                                      submenu.subMenus.map((_secondLevel) => (
                                        <li
                                          className={
                                            this.handlePathName(
                                              "/verbal",
                                              "Verbal",
                                              _secondLevel
                                            ) ||
                                            this.handlePathName(
                                              "/MCQ",
                                              "MCQ",
                                              _secondLevel
                                            ) ||
                                            this.handlePathName(
                                              "/coding",
                                              "Coding",
                                              _secondLevel
                                            )
                                              ? "question-user-selected"
                                              : null
                                          }
                                        >
                                          {this.NavComponent(_secondLevel)}
                                        </li>
                                      ))}
                                  </li>
                                ))}
                            </div>
                          </li>
                        </div>
                      );
                    } else if (sublink.heading == "Add Job Title") {
                      return (
                        <div key={subIndex}>
                          <li
                            key={("sublink-", sublink.heading)}
                            className={
                              window.location.pathname.includes("/jd") &&
                              sublink.heading == "Add Job Title"
                                ? "large-user-selected"
                                : null
                            }
                          >
                            {this.NavComponent(sublink)}
                          </li>
                        </div>
                      );
                    } else {
                      return (
                        <div key={subIndex}>
                          <li
                            key={("sublink-", sublink.heading)}
                            className={
                              window.location.pathname.includes("/jd") &&
                              sublink.heading == "JD List"
                                ? "large-user-selected"
                                : window.location.pathname.includes(
                                    "/resumeSourcing"
                                  ) && sublink.heading == "Profile Sourcing"
                                ? "large-user-selected"
                                : // : window.location.pathname.includes(
                                //     "/Schedule"
                                //   ) && sublink.heading == "Schedule"
                                // ? "large-user-selected"
                                window.location.pathname.includes(
                                    "/Settings"
                                  ) && sublink.heading == "Settings"
                                ? "large-user-selected"
                                : window.location.pathname.includes(
                                    "/dashboard"
                                  ) && sublink.heading == "Dashboard"
                                ? "large-user-selected"
                                : null
                            }
                          >
                            {this.NavComponent(sublink)}
                          </li>
                        </div>
                      );
                    }
                  })}
                </React.Fragment>
              );
            })}
          </ul>
        </>
      </nav>
    );
  }
}
export default SideNav;
