import React, { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import Button from "../../components/Controls/Button/Button";
import history from "../../routes/History";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import * as Actions from "../../store/User/UserAction";
import Profile from "../../assets/images/profile2.png";
import { logout } from "../../store/User/UserAction";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import ArrowDropDownIcon from "@material-ui/icons/ArrowDropDown";
import "./Navbar.css";

function Navbar(props) {
  const [anchorEl, setAnchorEl] = useState(null);
  const dispatch = useDispatch();
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const { isLoggedOut } = props.UserReducer;
  useEffect(() => {
    if (isLoggedOut) {
      localStorage.clear();
      history.push("/login");
    }
  }, [isLoggedOut]);

  const handleClose = (event, reason) => {
    setAnchorEl(null);
    props.UserAction.logout({
      refreshToken: localStorage.getItem("refresh_token"),
    });
    if (reason === "clickaway") {
      return;
    }
  };
  const handleClose1 = () => {
    setAnchorEl(null);
  };

  return (
    <>
      <nav
        style={{
          width: "100%",
          display: "flex",
          justifyContent: "space-between",
        }}
        className="navbar bg-white"
      >
        {window.location.pathname.includes("/dashboard") ? (
          <a className="navbar-brand title" id="title">
            Dashboard
          </a>
        ) : window.location.pathname.includes("/addjobtitle") ? (
          <a className="navbar-brand title" id="title">
            Add Job Title
          </a>
        ) : window.location.pathname.includes("/addquestiondata") ? (
          <a className="navbar-brand title" id="title">
            Questions
          </a>
        ) : window.location.pathname.includes("/jd") ? (
          <a className="navbar-brand title" id="title">
            Job Description List
          </a>
        ) : window.location.pathname.includes("/addjobtitle") ? (
          <a className="navbar-brand title" id="title">
            Add Jd Title
          </a>
        ) : window.location.pathname.includes("/AddJD") ? (
          <a className="navbar-brand title" id="title">
            Add Job Description
          </a>
        ) : window.location.pathname.includes("/jddata") ? (
          <a className="navbar-brand title" id="title">
            Job Description Data
          </a>
        ) : window.location.pathname.includes("/Schedule") ? (
          <a className="navbar-brand title" id="title">
            Schedule
          </a>
        ) : window.location.pathname.includes("/resumeSourcing") ? (
          <a className="navbar-brand title" id="title">
            Add Profile Sourcing
          </a>
        ) : window.location.pathname.includes("/followUp") ? (
          <a className="navbar-brand title" id="title">
            Follow Up List
          </a>
        ) : window.location.pathname.includes("/response") ? (
          <a className="navbar-brand title" id="title">
            Response
          </a>
        ) : window.location.pathname.includes("/questions") ? (
          <a className="navbar-brand title" id="title">
            Add Questions
          </a>
        ) : window.location.pathname.includes("/selected") ? (
          <a className="navbar-brand title" id="title">
            Selected
          </a>
        ) : window.location.pathname.includes("/shortlist") ? (
          <a className="navbar-brand title" id="title">
            Shortlist
          </a>
        ) : window.location.pathname.includes("/timeline") ? (
          <a className="navbar-brand title" id="title">
            Timeline
          </a>
        ) : window.location.pathname.includes("/user-management") ? (
          <a className="navbar-brand title" id="title">
            User Management
          </a>
        ) : window.location.pathname.includes("/Settings") ? (
          <a className="navbar-brand title" id="title">
            User Settings
          </a>
        ) : window.location.pathname.includes("/assessment/step1") ? (
          <a className="navbar-brand title" id="title">
            Create Assessment
          </a>
        ) : window.location.pathname.includes("/assessment/step2") ? (
          <a className="navbar-brand title" id="title">
            Create Assessment
          </a>
        ) : window.location.pathname.includes("/verbal") ? (
          <a className="navbar-brand title" id="title">
            Verbal Questions
          </a>
        ) : window.location.pathname.includes("/MCQ") ? (
          <a className="navbar-brand title" id="title">
            Mcq Questions
          </a>
        ) : window.location.pathname.includes("/questions") ? (
          <a className="navbar-brand title" id="title">
            Verbal Questions
          </a>
        ) : window.location.pathname.includes("/assessmentList") ? (
          <a className="navbar-brand title" id="title">
            List of Assessments
          </a>
        ) : window.location.pathname.includes("/schedule-list") ? (
          <a className="navbar-brand title" id="title">
            Schedule List
          </a>
        ) : window.location.pathname.includes("/history") ? (
          <a className="navbar-brand title" id="title">
            History: {props.label}
          </a>
        ) : window.location.pathname.includes("/invites") ? (
          <a className="navbar-brand title" id="title">
            Invites:{props.label}
          </a>
        ) : null}
        <div>
          <div className="navbar-profile">
            {/* <img src={Profile} className="image-profile" /> */}
            <span className="navbar-profile-name">
              {localStorage.getItem("name")}
            </span>
            <span
              style={{ cursor: "pointer" }}
              aria-controls="simple-menu"
              aria-haspopup="true"
              onClick={handleClick}
            >
              <ArrowDropDownIcon />
            </span>
            <Menu
              id="menu-appbar"
              getContentAnchorEl={null}
              className="menuItem-position"
              anchorEl={anchorEl}
              keepMounted
              open={Boolean(anchorEl)}
              onClose={handleClose1}
            >
              <MenuItem
                onClick={handleClose}
                style={{ backgroundColor: "#fff" }}
              >
                Logout
              </MenuItem>
            </Menu>
          </div>
        </div>
      </nav>
    </>
  );
}

const mapStateToProps = (state) => {
  return {
    // FeatureReducer : state.FeatureReducer
    UserReducer: state.UserReducer,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    UserAction: bindActionCreators(Actions, dispatch),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Navbar);
