import * as types from "./scheduleTypes";

export function createSchedule(payload) {
  return {
    type: types.CREATE_SCHEDULE,
    payload: payload,
  };
}
export function createScheduleSuccess(payload) {
  return {
    type: types.CREATE_SCHEDULE_SUCCESS,
    payload: payload,
  };
}
export function createScheduleError(error) {
  return {
    type: types.CREATE_SCHEDULE_ERROR,
    payload: error,
  };
}
