import { call, put } from "redux-saga/effects";
import * as ACTIONS from "./QuestionCreateAction";
import { takeLatest } from "redux-saga/effects";
import * as TYPES from "./QuestionCreateTypes";
import apiJunction from "../../utils/api";
import serverAddress from "../../config";
import { store } from "react-notifications-component";
const candid = localStorage.getItem("id");

export function* addQuestionData(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "post",
      url: `${serverAddress}/api/createquestion/create`,
      body: action.payload,
    });
    if (result) {
      yield put(ACTIONS.addQuestionDataSuccess(result));
      store.addNotification({
        title: "Success",
        message: "Question Create successfully!!",
        type: "success",
        container: "bottom-center",
        animationIn: ["animated", "fadeIn"],
        animationOut: ["animated", "fadeOut"],
        dismiss: {
          duration: 3000,
        },
      });
    }
  } catch (e) {
    yield put(ACTIONS.addQuestionDataError(e.response));
    store.addNotification({
      title: "Failed",
      message: "Question is Not Created",
      type: "danger",
      container: "bottom-center",
      animationIn: ["animated", "fadeIn"],
      animationOut: ["animated", "fadeOut"],
      dismiss: {
        duration: 3000,
      },
    });
  }
}

export function* getQuestionData() {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "get",
      url: `${serverAddress}/api/createquestion/get/${candid}`,
    });
    if (result) {
      yield put(ACTIONS.getQuestionDataSuccess(result));
    }
  } catch (e) {
    yield put(ACTIONS.getQuestionDataError(e.response));
  }
}

export function* updateQuestionData(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "patch",
      url: ``,
      body: action.payload,
    });
    if (result) {
      yield put(ACTIONS.updateQuestionDataSuccess(result));
    }
  } catch (e) {
    yield put(ACTIONS.updateQuestionDataError(e.response));
  }
}

export function* deleteQuestionData(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "delete",
      url: ``,
      body: action.payload,
    });
    if (result) {
      yield put(ACTIONS.deleteQuestionDataSuccess(result));
    }
  } catch (e) {
    yield put(ACTIONS.deleteQuestionDataError(e.response));
  }
}

//get by id

export function* getQuestionByIdData(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "get",
      url: `${serverAddress}/api/createquestion/getById/${action.payload.id}`,
    });
    if (result) {
      yield put(ACTIONS.getQuestionByIdSuccess(result));
    }
  } catch (e) {
    yield put(ACTIONS.getQuestionByIdError(e.response));
  }
}

export function* QuestionCreateSaga() {
  yield takeLatest(TYPES.CREATE_NEWQUESTION, addQuestionData);
  yield takeLatest(TYPES.GET_NEWQUESTION, getQuestionData);
  yield takeLatest(TYPES.UPDATE_NEWQUESTION, updateQuestionData);
  yield takeLatest(TYPES.DELETE_NEWQUESTION, deleteQuestionData);
  yield takeLatest(TYPES.GET_QUESTION_BY_ID, getQuestionByIdData);
}
