import * as types from "./DashboardTypes";

const INITIAL_STATE = {
  getDashboardData: [],
  isGetDashboardData: false,
  isDateFilterDashboardData: false,
};

export function DashboardReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case types.GET_DASHBOARD:
      return {
        ...state,
        getDashboardData: [],
        isGetDashboardData: false,
      };

    case types.GET_DASHBOARD_SUCCESS:
      return {
        ...state,
        getDashboardData: action.payload,
        isGetDashboardData: true,
      };

    case types.GET_DASHBOARD_ERROR:
      return {
        ...state,
        getDashboardData: [],
        isGetDashboardData: false,
      };
    case types.DATE_FILTER_DASHBOARD:
      return {
        ...state,
        isDateFilterDashboardData: false,
      };

    case types.DATE_FILTER_DASHBOARD_SUCCESS:
      return {
        ...state,
        getDashboardData: action.payload,
        isDateFilterDashboardData: true,
      };

    case types.DATE_FILTER_DASHBOARD_ERROR:
      return {
        ...state,
        isDateFilterDashboardData: false,
      };

    default:
      return state;
  }
}
