import React, { useEffect } from "react";
import Modal from "@material-ui/core/Modal";
import SendIcon from "@material-ui/icons/Send";
import AttachFileIcon from "@material-ui/icons/AttachFile";
import RichTextEditor from "react-rte";
import TextField from "@material-ui/core/TextField";
import { makeStyles } from "@material-ui/core/styles";
import ReactFileReader from "react-file-reader";
import Button from "@material-ui/core/Button";
import { Link } from "react-router-dom";
import SkillsBox from "../Controls/Skills/skillBox";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import CheckIcon from "@material-ui/icons/Check";
import ClearIcon from "@material-ui/icons/Clear";
import ChatIcon from "@material-ui/icons/Chat";
import FiberManualRecordIcon from "@material-ui/icons/FiberManualRecord";
import DeleteIcon from "@material-ui/icons/Delete";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import Card from "@material-ui/core/Card";
import SearchIcon from "@material-ui/icons/Search";
import { CardContent, CardActions, FormHelperText } from "@material-ui/core";
import { NonceProvider } from "react-select";
import * as Actions from "../../store/Sendmessage/SendAction";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import "./sendmessage.css";
import Email from "../Controls/Email/Email";
import succcessIcon from "../../assets/images/successIcon.svg";

const useStyles = makeStyles(() => ({
  footer: {
    position: "fixed",
    left: 0,
    bottom: 0,
  },

  modal: {
    bottom: 0,
    left: 0,
    position: "absolute",
    borderRadius: 6,
    outline: "none",
    backgroundColor: "white",
    maxwidth: 490,
    height: 450,
    margin: "auto",
    boxShadow: "2 10 15 #00000029",
    marginTop: 100,
    justifyContent: "center",
    alignItems: "center",
    fontFamily: "Poppins",
    fontSize: 16,
  },
}));

function Sendmessage(props) {
  const classes = useStyles();

  return (
    <CardContent>
      <div className="emailcss">
        <Modal
          open={props.open}
          onClose={props.onClose}
          // setEmailMsg={props.setEmailMsg}
          // onBodyUpdate={props.onBodyUpdate}
          // onMailSend={props.onMailSend}
          // onMailFile={props.handleMailFiles}
        >
          <Card className={classes.modal}>
            <CardContent>
              <div
                style={{
                  display: "flex",
                  justifyContent: "space-between",
                  margin: "5px",
                }}
              >
                <strong>Send Message</strong>
                <Button>
                  <ClearIcon onClick={props.onClose} />
                </Button>
              </div>
              <hr></hr>
              <div className="pos-skill">
                <br />
                <span className="toEmail">To:</span>
                <Email
                  emailArr={props.setEmailMsg}
                  // removeEmail={removeEmail}
                />
              </div>

              <div className="skill-box-2">
                <p>Dear,</p>
                <div className="skill-box-2-inner">
                  {props.setEmailMsg &&
                    props.setEmailMsg[0] &&
                    props.setEmailMsg[0].name}
                </div>
              </div>
              <RichTextEditor
                className="richText"
                onChange={props.onBodyUpdate}
                value={props.msgBody}
              />

              <div className="footer">
                <div className="footer-class">
                  <ReactFileReader handleFiles={props.handleMailFiles}>
                    <span className="attach-file">
                      <AttachFileIcon fontSize="small" />
                      Attach File
                    </span>
                  </ReactFileReader>
                  {/* <iframe
                    src={props.mailFile}
                    frameBorder="0"
                    height="400"
                    width="50%"
                  /> */}
                  <div>
                    <Button
                      className="buttonclass-1"
                      onClick={props.onMailSend}
                    >
                      <span className="send-position-2"> Send</span>
                      <SendIcon className="send-icon-pos" />
                    </Button>
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>
        </Modal>
      </div>
      {/* <Modal open={handleOpenSuccessModal} onClose={handleCloseSuccessModal}>
        <Card className="success-modal-card">
          <div>
            <img className="img-position" src={succcessIcon} alt=""></img>
            <div className="flex-4">
              <div className="content-props">
                <strong>JD Added Successfully</strong>
              </div>
            </div>
          </div>
        </Card>
      </Modal> */}
    </CardContent>
  );
}

const mapStateToProps = (state) => {
  return {
    SendReducer: state.SendReducer,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    SendAction: bindActionCreators(Actions, dispatch),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Sendmessage);
