import { call, put } from "redux-saga/effects";
import * as ACTIONS from "./AssessmentAction";
import { takeLatest } from "redux-saga/effects";
import * as TYPES from "./AssessmentTypes";
import apiJunction from "../../utils/api";
import serverAddress from "../../config";
import { store } from "react-notifications-component";

export function* getIndustryTypes(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "get",
      url: `${serverAddress}/industrialType`,
    });
    if (result) {
      yield put(ACTIONS.getIndustryTypesSuccess(result));
    }
  } catch (e) {
    yield put(ACTIONS.getIndustryTypesError(e.response));
  }
}

