export function* getQuestionTypes(action) {
  const urltype = action.payload
    ? `${serverAddress}/questionType?name=${action.payload.name}`
    : `${serverAddress}/questionType`;
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "get",
      url: urltype,
    });
    if (result) {
      yield put(ACTIONS.getQuestionTypesSuccess(result));
    }
  } catch (e) {
    yield put(ACTIONS.getQuestionTypesError(e.response));
  }
}

