export function* createTest(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "post",
      url: `${serverAddress}/test`,
      body: action.payload,
    });
    if (result) {
      yield put(ACTIONS.addTestSuccess(result));
      store.addNotification({
        title: "Success",
        message: "Test created successfully!!",
        type: "success",
        container: "top-center",
        animationIn: ["animated", "fadeIn"],
        animationOut: ["animated", "fadeOut"],
        dismiss: {
          duration: 2000,
        },
      });
    }
  } catch (e) {
    yield put(ACTIONS.addTestError(e.response));
  }
}

