export function* getQuestions(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "get",
      url: action.payload.searchTerm
        ? `${serverAddress}/question?page=${action.payload.page}&limit=${action.payload.limit}&type=${action.payload.type}&skills=${action.payload.searchTerm}`
        : action.payload.type
        ? `${serverAddress}/question?page=${action.payload.page}&limit=${action.payload.limit}&type=${action.payload.type}`
        : `${serverAddress}/question`,
    });
    if (result) {
      yield put(ACTIONS.getQuestionsSuccess(result));
    }
  } catch (e) {
    yield put(ACTIONS.getQuestionsError(e.response));
  }
}

export function* getQuestionById(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "get",
      url: ``,
    });
    if (result) {
      yield put(ACTIONS.getQuestionByIdSuccess(result));
    }
  } catch (e) {
    yield put(ACTIONS.getQuestionByIdError(e.response));
  }
}

export function* updateQuestion(action, id) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "patch",
      url: `${serverAddress}/question/${action.id}`,
      body: action.payload,
    });
    if (result && result.status == 200) {
      yield put(ACTIONS.updateQuestionSuccess(result));
      store.addNotification({
        title: "Success",
        message: "Data Updated successfully",
        type: "success",
        container: "top-center",
        animationIn: ["animated", "fadeIn"],
        animationOut: ["animated", "fadeOut"],
        dismiss: {
          duration: 2000,
        },
      });
    }
  } catch (e) {
    yield put(ACTIONS.updateQuestionError(e.response));
    store.addNotification({
      title: "Error",
      message: "Data Not Updated",
      type: "danger",
      container: "top-center",
      animationIn: ["animated", "fadeIn"],
      animationOut: ["animated", "fadeOut"],
      dismiss: {
        duration: 2000,
      },
    });
  }
}

export function* deleteQuestion(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "delete",
      url: `${serverAddress}/question/${action.payload.id}`,
    });
    if (result && (result.status==200 || result.status==204)) {
      yield put(ACTIONS.deleteQuestionSuccess(result));
      store.addNotification({
        title: "Success",
        message: "Data Deleted successfully",
        type: "success",
        container: "top-center",
        animationIn: ["animated", "fadeIn"],
        animationOut: ["animated", "fadeOut"],
        dismiss: {
          duration: 2000,
        },
      });
    }else{
      store.addNotification({
        title: "Error",
        message: "Data Not Deleted",
        type: "danger",
        container: "top-center",
        animationIn: ["animated", "fadeIn"],
        animationOut: ["animated", "fadeOut"],
        dismiss: {
          duration: 2000,
        },
      });
    }
  } catch (e) {
    yield put(ACTIONS.deleteQuestionError(e.response));
    store.addNotification({
      title: "Error",
      message: "Data Not Deleted",
      type: "danger",
      container: "top-center",
      animationIn: ["animated", "fadeIn"],
      animationOut: ["animated", "fadeOut"],
      dismiss: {
        duration: 2000,
      },
    });
  }
}

