export function* getAllTest(action) {
  const { startDate, endDate, jdId, type, limit, page } = action.payload;
  const url = `${serverAddress}/test?page=${page ? page : 0}&limit=${
    limit ? limit : 0
  }${jdId ? "&jdId=" + jdId : ""}${startDate ? "&fromDate=" + startDate : ""}${
    endDate ? "&toDate=" + endDate : ""
  }${type ? "&type=" + type : ""}`;

  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "get",
      url: url,
    });
    if (result) {
      yield put(ACTIONS.getAllTestSuccess(result));
    }
  } catch (e) {
    yield put(ACTIONS.getAllTestError(e.response));
  }
}

