export function* AssessmentSaga() {
  yield takeLatest(TYPES.GET_INDUSTRY_TYPE, getIndustryTypes);
  yield takeLatest(TYPES.GET_JOB_TYPE, getJobTypes);
  yield takeLatest(TYPES.GET_QUESTION_TYPE, getQuestionTypes);
  yield takeLatest(TYPES.GET_EXPERIENCE_LEVEL, getExperienceLevel);
  yield takeLatest(TYPES.CREATE_TEST, createTest);
  yield takeLatest(TYPES.CREATE_QUESTIONS, createQuestion);
  yield takeLatest(TYPES.GET_QUESTIONS, getQuestions);
  yield takeLatest(TYPES.GET_QUESTION_BY_ID, getQuestionById);
  yield takeLatest(TYPES.UPDATE_QUESTION, updateQuestion);
  yield takeLatest(TYPES.DELETE_QUESTION, deleteQuestion);
  yield takeLatest(TYPES.GET_TEST_BY_ID, getTestById);
  yield takeLatest(TYPES.UPDATE_TEST, updateTest);
  yield takeLatest(TYPES.DELETE_TEST, deleteTest);
  yield takeLatest(TYPES.GET_ALL_TEST, getAllTest);
  // yield takeLatest(TYPES.GET_ALL_SKILLS, getAllSkills);
  yield takeLatest(TYPES.GET_ALL_LANGUAGES, getAllLanguages);
}
