//send message
export function* sendMessage(action) {
  let payload = {
    to: action.payload.to,
    medium: action.payload.medium,
    body: action.payload.body,
    inviteId: action.payload.inviteId,
  };
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "post",
      url: action.payload.bulk
        ? `${serverAddress}/conversation/message-bulk`
        : `${serverAddress}/conversation/message`,
      body: payload,
    });
    if (result) {
      yield put(ACTIONS.createMessageSuccess(result));
      store.addNotification({
        title: "Success",
        message: "Message sent successfully!!",
        type: "success",
        container: "top-center",
        animationIn: ["animated", "fadeIn"],
        animationOut: ["animated", "fadeOut"],
        dismiss: {
          duration: 2000,
        },
      });
    }
  } catch (e) {
    yield put(ACTIONS.createMessageError(e.response));
    store.addNotification({
      title: "Error",
      message: "Error Occurred!",
      type: "danger",
      container: "top-center",
      animationIn: ["animated", "fadeIn"],
      animationOut: ["animated", "fadeOut"],
      dismiss: {
        duration: 2000,
      },
    });
  }
}

