//send template message
export function* sendTemplateMessage(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "post",
      url: `${serverAddress}/conversation/send-template-message`,
      body: action.payload,
    });

    if (result && result.status === 200) {
      yield put(ACTIONS.sendTemplateMessageSuccess(result));
      store.addNotification({
        title: "Success",
        message: "Message sent successfully!!",
        type: "success",
        container: "top-center",
        animationIn: ["animated", "fadeIn"],
        animationOut: ["animated", "fadeOut"],
        dismiss: {
          duration: 2000,
        },
      });
    }
  } catch (e) {
    yield put(ACTIONS.sendTemplateMessageError(e.response));
    store.addNotification({
      title: "Error",
      message: "Error Occurred!",
      type: "danger",
      container: "top-center",
      animationIn: ["animated", "fadeIn"],
      animationOut: ["animated", "fadeOut"],
      dismiss: {
        duration: 2000,
      },
    });
  }
}

