export function* whatsappSeen(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "patch",
      url: `${serverAddress}/conversation/mark-seen?medium=${action.payload.medium}&inviteId=${action.payload.inviteId}`,
    });
    if (result) {
      yield put(ACTIONS.whatsappSeenSuccess(result));
    }
  } catch (e) {
    yield put(ACTIONS.whatsappSeenError(e.response));
  }
}
export function* ScheduleSaga() {
  yield takeLatest(TYPES.CREATE_SCHEDULE, sendSchedule);
  yield takeLatest(TYPES.CREATE_MESSAGE, sendMessage);
  yield takeLatest(TYPES.CREATE_EMAIL, sendEmail);
  yield takeLatest(TYPES.GET_SCHEDULE, getscheduleData);
  yield takeLatest(TYPES.GET_INVITES, getInvites);
  yield takeLatest(TYPES.GET_SCHEDULE_BY_ID, getscheduleById);
  yield takeLatest(TYPES.UPDATE_SCHEDULE, updateScheduleData);
  yield takeLatest(TYPES.GET_SCHEDULE_WITH_RESPONSE, getSchedule);
  yield takeLatest(TYPES.INVITE_HISTORY, inviteHistory);
  yield takeLatest(TYPES.GET_TEMPLATES, getTemplates);
  yield takeLatest(TYPES.SEND_TEMPLATE_MESSAGE, sendTemplateMessage);
  yield takeLatest(TYPES.WHATSAPP_SEEN, whatsappSeen);
}
