import { call, put } from "redux-saga/effects";
import * as ACTIONS from "./CommonAction";
import { takeLatest } from "redux-saga/effects";
import * as TYPES from "./CommonTypes";
import apiJunction from "../../utils/api";
import serverAddress from "../../config";

export function* getJobTitle() {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "get",
      url: `${serverAddress}/api/jobtitle/get`,
    });
    if (result && result.code == 200) {
      yield put(ACTIONS.getJobTitleSuccess(result));
    }
  } catch (e) {
    yield put(ACTIONS.getJobTitleError(e.response));
  }
}

export function* CommonSaga() {
  yield takeLatest(TYPES.GET_JOBTITLE, getJobTitle);
}
