import * as types from "./FeatureTypes";

const INITIAL_STATE = {
  getData: [],
};

// Replace with you own reducer
export function FeatureReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case types.GET_DATA_RECEIVE:
      return {
        ...state,
        getData: action.payload.data,
      };
    default:
      return state;
  }
}
