import axios from "./axios";
import local from "./localStorage";
// import { timeDifference } from "./timeDifference";
import * as UserAction from "../store/User/UserAction";
import store from "../store";

class ApiJunction {
  makeRequest(params) {
    store.dispatch(UserAction.incrementLoader());
    let token = params.token || local.getItem("token");
    axios.defaults.headers.common["Authorization"] = `Bearer ${token}`;
    axios.defaults.headers.common["Content-Type"] = `application/json`;
    // return axios[params.method](params.url, params.body)
    if (params.method === "get") {
      return axios.get(params.url, { params: params.params }).then((res) => {
        store.dispatch(UserAction.decrementLoader());
        if (res.code === 200) {
          return res.data;
        }
        return res;
      })
      .catch((err) => {
        store.dispatch(UserAction.decrementLoader());
        return err;
      });
    } else if (params.method === "post") {
      return axios
        .post(params.url, params.body)
        .then((res) => {
          store.dispatch(UserAction.decrementLoader());
          if (res.code === 200) {
            return res.data;
          }
          return res;
        })
        .catch((err) => {
          store.dispatch(UserAction.decrementLoader());
          return err;
        });
    } else if (params.method === "put") {
      return axios.put(params.url, params.body).then((res) => {
        if (res) {
          store.dispatch(UserAction.decrementLoader());
          return res;
        }
      })
      .catch((err) => {
        store.dispatch(UserAction.decrementLoader());
        return err;
      });
    } else if (params.method === "delete") {
      return axios.delete(params.url, params.body).then((res) => {
        if (res) {
          store.dispatch(UserAction.decrementLoader());
          return res;
        }
      })
      .catch((err) => {
        store.dispatch(UserAction.decrementLoader());
        return err;
      });
    } else if (params.method === "patch") {
      return axios.patch(params.url, params.body).then((res) => {
        if (res) {
          store.dispatch(UserAction.decrementLoader());
          return res;
        }
      })
      .catch((err) => {
        store.dispatch(UserAction.decrementLoader());
        return err;
      });;
    } else {
      return { success: false, msg: "No method provided, get, post?" };
    }
  }

  login(params) {
    return axios.post(params.url, params.body);
  }
}

export default new ApiJunction();
