import { call, put } from "redux-saga/effects";
import * as ACTIONS from "./PlandataAction";
import { takeLatest } from "redux-saga/effects";
import * as TYPES from "./PlandataTypes";
import apiJunction from "../../utils/api";
import serverAddress from "../../config";
import { store } from "react-notifications-component";

export function* addPlandata(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "post",
      url: `${serverAddress}/plan/`,
      body: action.payload,
    });
    if (result) {
      yield put(ACTIONS.addPlanDataSuccess(result));
      store.addNotification({
        title: "Success",
        message: "Plan Created successfully!!",
        type: "success",
        container: "bottom-center",
        animationIn: ["animated", "fadeIn"],
        animationOut: ["animated", "fadeOut"],
        dismiss: {
          duration: 2000,
        },
      });
    }
  } catch (e) {
    yield put(ACTIONS.addPlanDataError(e.response));
    store.addNotification({
      title: "Error",
      message: "Fill All Plan Data and submit",
      type: "danger",
      container: "top-center",
      animationIn: ["animated", "fadeIn"],
      animationOut: ["animated", "fadeOut"],
      dismiss: {
        duration: 2000,
      },
    });
  }
}

export function* getPlanData() {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "get",
      url: `${serverAddress}/plan/`,
    });
    if (result) {
      yield put(ACTIONS.getPlanDataSuccess(result));
    }
  } catch (e) {
    yield put(ACTIONS.getPlanDataError(e.response));
  }
}

export function* PlanSaga() {
  yield takeLatest(TYPES.CREATE_PLAN, addPlandata);
  yield takeLatest(TYPES.GET_PLAN, getPlanData);
}
