import { call, put } from "redux-saga/effects";
import * as ACTIONS from "./SendAction";
import { takeLatest } from "redux-saga/effects";
import * as TYPES from "./SendTypes";
import apiJunction from "../../utils/api";
import serverAddress from "../../config";
import { store } from "react-notifications-component";

export function* addSendData(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "post",
      url: `${serverAddress}/api/sendmail/create`,
      body: action.payload,
    });
    if (result) {
      yield put(ACTIONS.addSendDataSuccess(result));
      store.addNotification({
        title: "Success",
        message: "Mail sent successfully!!",
        type: "success",
        container: "top-center",
        animationIn: ["animated", "fadeIn"],
        animationOut: ["animated", "fadeOut"],
        dismiss: {
          duration: 2000,
        },
      });
    }
  } catch (e) {
    yield put(ACTIONS.addSendDataError(e.response));
  }
}

export function* SendSaga() {
  yield takeLatest(TYPES.CREATE_SENDMESSAGE, addSendData);
}
