import { call, put } from "redux-saga/effects";
import * as ACTIONS from "./Response.Action";
import { takeLatest } from "redux-saga/effects";
import * as TYPES from "./Response.Types";
import apiJunction from "../../utils/api";
import serverAddress from "../../config";
import { store } from "react-notifications-component";
import history from "../../routes/History";
const candid = localStorage.getItem("id");

export function* addResponseData(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "post",
      url: `${serverAddress}/api/response/create`,
      body: action.payload,
    });
    if (result) {
      yield put(ACTIONS.addResponseDataSuccess(result));
      store.addNotification({
        title: "Success",
        message: "Response created successfully!!",
        type: "success",
        container: "top-center",
        animationIn: ["animated", "fadeIn"],
        animationOut: ["animated", "fadeOut"],
        dismiss: {
          duration: 2000,
        },
      });
    }
  } catch (e) {
    yield put(ACTIONS.addResponseDataError(e.response));
    store.addNotification({
      title: "Error",
      message: "Fill All Data and submit",
      type: "danger",
      container: "top-center",
      animationIn: ["animated", "fadeIn"],
      animationOut: ["animated", "fadeOut"],
      dismiss: {
        duration: 2000,
      },
    });
  }
}

export function* getResponseData(action) {
  const {
    page,
    limit,
    from,
    to,
    jdId,
    status,
    scheduleId,
    searchKey,
    score,
    mcqScore,
    verbalScore,
    sort,
    topPercent,
    topCount,
    isSeen,
    codingScore,
  } = action.payload;
  var pattern = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
  const isEmail = searchKey ? pattern.test(searchKey) : false;

  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "get",
      url: `${serverAddress}/response?page=${page ? page : 0}&limit=${
        limit ? limit : 0
      }${jdId ? "&jdId=" + jdId : ""}${from ? "&fromDate=" + from : ""}${
        to ? "&toDate=" + to : ""
      }${status ? "&status=" + status : ""}${
        scheduleId ? "&scheduleId=" + scheduleId : ""
      }${
        !searchKey
          ? ""
          : isEmail
          ? "&candidateEmail=" + searchKey
          : "&candidateName=" + searchKey
      }${score ? "&score=" + score : ""}${
        verbalScore ? "&verbalScore=" + verbalScore : ""
      }${mcqScore ? "&mcqScore=" + mcqScore : ""}${
        codingScore ? "&codingScore=" + codingScore : ""
      }${sort ? "&sortBy=" + sort : ""}${
        topCount ? "&topCount=" + topCount : ""
      }${topPercent ? "&topPercent=" + topPercent : ""}${
        isSeen === true || isSeen === false ? "&isSeen=" + isSeen : ""
      }
      `,
      body: action.payload,
    });
    if (result) {
      yield put(ACTIONS.getResponseDataSuccess(result));
    }
  } catch (e) {
    yield put(ACTIONS.getResponseDataError(e.response));
  }
}
