export function* ResponseSaga() {
  yield takeLatest(TYPES.CREATE_RESPONSE, addResponseData);
  yield takeLatest(TYPES.GET_RESPONSE, getResponseData);
  yield takeLatest(TYPES.SEARCH_RESPONSE, getResponseData);
  yield takeLatest(TYPES.DELETE_RESPONSE, deleteResponseData);
  // yield takeLatest(TYPES.SEARCH_RESPONSE, searchResponseData);
  yield takeLatest(TYPES.GET_RESPONSE_BY_ID, getResponseByIdData);
  yield takeLatest(TYPES.UPDATE_RESPONSE, updateResponseData);
  yield takeLatest(TYPES.FILTER_BY_DATE, filterByDate);
  yield takeLatest(TYPES.ADD_COMMENT, addComment);
  yield takeLatest(TYPES.UPDATE_COMMENT, updateComment);
  yield takeLatest(TYPES.SHARE_CREATE, shareCreate);
  yield takeLatest(TYPES.SHARE_MULTI_CREATE, shareMultiCreate);
  yield takeLatest(TYPES.SHARE_GET, shareGet);
  yield takeLatest(TYPES.SHARE_MAIL, shareEmail);
  yield takeLatest(TYPES.SHARE_MULTI_MAIL, shareEmailMulti);
  yield takeLatest(TYPES.SHARE_GET_MULTI, shareGetMulti);
  yield takeLatest(TYPES.UPDATE_MULTI_RESPONSE, updateMulti);
  yield takeLatest(TYPES.GET_CODING_RESPONSE, getCodingRes);
  yield takeLatest(TYPES.UPDATE_CODING_SCORE, updateCodingScore);
}
