import React, { Component } from "react";
import CloseIcon from "@material-ui/icons/Close";
import "./skillBox.css";

class SkillsBox extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <div className="card">
        <div className="row">
          {this.props.skillArr.map((item, index) => {
            return (
              <div className="items" key={item.id}>
                <div
                  onClick={() => this.props.removeSkill(item.id)}
                  className="icon"
                >
                  <CloseIcon style={{ fontSize: "medium" }} />
                </div>
                &nbsp;&nbsp;
                <div className="text">{item.skill}</div>
              </div>
            );
          })}
        </div>
      </div>
    );
  }
}

export default SkillsBox;
