import { combineReducers } from "redux";
import loader from "../Loader/LoaderReducer";
import snackbar from "../Snackbar/SnackbarReducer";
import { FeatureReducer } from "../Feature1/FeatureReducer";
import { QuestionReducer } from "../Question/QuestionReducer";
import { UserReducer } from "../User/UserReducer";
import { CandidateReducer } from "../Candidate/CandidateReducer";
import { JobDescriptionReducer } from "../JobDescription/JobDescriptionReducer";
import { ScheduleReducer } from "../Schedule/scheduleReducer";
import { QuestionCreateReducer } from "../QuestionCreate/QuestionCreateReducer";
import { CommonReducer } from "../Common/CommonReducer";
import { resumesourceReducer } from "../Resumesource/resumesourceReducer";
import { JobDataReducer } from "../JobData/JobDataReducer";
import { SendReducer } from "../Sendmessage/SendReducer";
import { DashboardReducer } from "../Dashboard/DashboardReducer";
import { responseReducer } from "../Response/Response.Reducer";
import { PlanReducer } from "../Plandata/PlandataReducer";
import { AssessmentReducer } from "../Assessment/AssessmentReducer";

export default combineReducers({
  loader,
  snackbar,
  FeatureReducer,
  QuestionReducer,
  UserReducer,
  CandidateReducer,
  JobDescriptionReducer,
  ScheduleReducer,
  CommonReducer,
  QuestionCreateReducer,
  resumesourceReducer,
  JobDataReducer,
  SendReducer,
  DashboardReducer,
  responseReducer,
  PlanReducer,
  AssessmentReducer
});
