import { call, put } from "redux-saga/effects";
import * as ACTIONS from "./CandidateAction";
import { takeLatest } from "redux-saga/effects";
import * as TYPES from "./CandidateTypes";
import apiJunction from "../../utils/api";
import serverAddress from "../../config";
import { store } from "react-notifications-component";

export function* addCandidateData(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "post",
      url: `${serverAddress}/api/Candidate/create`,
      body: action.payload,
    });
    if (result) {
      yield put(ACTIONS.addCandidateDataSuccess(result));
      store.addNotification({
        title: "Success",
        message: "Candidate created successfully!!",
        type: "success",
        container: "top-center",
        animationIn: ["animated", "fadeIn"],
        animationOut: ["animated", "fadeOut"],
        dismiss: {
          duration: 2000,
        },
      });
    }
  } catch (e) {
    yield put(ACTIONS.addCandidateDataError(e.response));
  }
}

export function* getCandidateData(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "get",
      url: `${serverAddress}/api/Candidate/get?page=${action.payload.page}&limit=${action.payload.limit}`,
    });
    if (result) {
      yield put(ACTIONS.getCandidateDataSuccess(result));
    }
  } catch (e) {
    yield put(ACTIONS.getCandidateDataError(e.response));
  }
}
