export function* CandidateSaga() {
  yield takeLatest(TYPES.CREATE_CANDIDATE, addCandidateData);
  yield takeLatest(TYPES.GET_CANDIDATE, getCandidateData);
  // yield takeLatest(TYPES.DELETE_CANDIDATE, deleteCandidateData);
}
