import * as types from "./PlandataTypes";

const INITIAL_STATE = {
  isLoggedIn: false,
  planData: [],
  isPlanAdded: false,
  getPlanData: [],
  isGetPlanData: false,
  isQuestionDataUpdated: false,
  isQuestionDeleted: false,
};

export function PlanReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case types.CREATE_PLAN:
      return {
        ...state,
        isPlanAdded: false,
      };

    case types.CREATE_PLAN_SUCCESS:
      return {
        ...state,
        isPlanAdded: true,
        planData: action.payload,
      };

    case types.CREATE_PLAN_ERROR:
      return {
        ...state,
        isPlanAdded: false,
      };

    case types.GET_PLAN:
      return {
        ...state,
        isGetPlanData: false,
      };

    case types.GET_PLAN_SUCCESS:
      return {
        ...state,
        getPlanData: action.payload,
        isGetPlanData: true,
      };

    case types.GET_PLAN_ERROR:
      return {
        ...state,
        isGetPlanData: false,
      };

    default:
      return state;
  }
}
