import * as types from "./AssessmentTypes";

const INITIAL_STATE = {
  industryTypes: [],
  isGetIndustryTypes: false,
  jobTypes: [],
  isGetJobTypes: false,
  experienceLevel: [],
  isGetExperienceLevel: false,
  test: [],
  isTestAdded: false,
  questionCreated: [],
  isQuestionAdded: false,
  questions: [],
  isGetQuestions: false,
  questionById: [],
  isGetQuestionById: false,
  isQuestionUpdated: false,
  isQuestionDeleted: false,
  testById: [],
  isGetTestById: false,
  isTestUpdated: false,
  isTestDeleted: false,
  allTest: [],
  isGetAllTest: false,
  // allSkills: [],
  // isGetAllSkills: false,
  questionType:[],
  isGetQuestionTypes:false,
  questionTypes:[],
  allLanguages: [],
  isGetAllLanguages: false,
};

export function AssessmentReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case types.GET_INDUSTRY_TYPE:
      return {
        ...state,
        isGetIndustryTypes: false,
      };

    case types.GET_INDUSTRY_TYPE_SUCCESS:
      return {
        ...state,
        industryTypes: action.payload,
        isGetIndustryTypes: true,
      };

    case types.GET_INDUSTRY_TYPE_ERROR:
      return {
        ...state,
        isGetIndustryTypes: false,
      };
      case types.GET_QUESTION_TYPE:
        return {
          ...state,
          isGetQuestionTypes: false,
        };
  
      case types.GET_QUESTION_TYPE_SUCCESS:
        return {
          ...state,
          questionType: action.payload,
          isGetQuestionTypes: true,
        };
  
      case types.GET_QUESTION_TYPE_ERROR:
        return {
          ...state,
          isGetQuestionTypes: false,
        };

    case types.GET_JOB_TYPE:
      return {
        ...state,
        isGetJobTypes: false,
      };

    case types.GET_JOB_TYPE_SUCCESS:
      return {
        ...state,
        jobTypes: action.payload,
        isGetJobTypes: true,
      };
      case types.GET_QUESTION_TYPE_SUCCESS:
      return {
        ...state,
        questionType:action.payload
      };

    case types.GET_JOB_TYPE_ERROR:
      return {
        ...state,
        isGetJobTypes: false,
      };

    case types.GET_EXPERIENCE_LEVEL:
      return {
        ...state,
        isGetExperienceLevel: false,
      };

    case types.GET_EXPERIENCE_LEVEL_SUCCESS:
      return {
        ...state,
        experienceLevel: action.payload,
        isGetExperienceLevel: true,
      };

    case types.GET_EXPERIENCE_LEVEL_ERROR:
      return {
        ...state,
        isGetExperienceLevel: false,
      };

    case types.CREATE_TEST:
      return {
        ...state,
        isTestAdded: false,
      };

    case types.CREATE_TEST_SUCCESS:
      return {
        ...state,
        isTestAdded: true,
        test: action.payload,
      };

    case types.CREATE_TEST_ERROR:
      return {
        ...state,
        isTestAdded: false,
      };

    case types.CREATE_QUESTIONS:
      return {
        ...state,
        isQuestionAdded: false,
      };

    case types.CREATE_QUESTION_SUCCESS:
      return {
        ...state,
        isQuestionAdded: true,
        questionCreated: action.payload,
      };

    case types.CREATE_QUESTION_ERROR:
      return {
        ...state,
        isQuestionAdded: false,
      };

    case types.GET_QUESTIONS:
      return {
        ...state,
        isGetQuestions: false,
      };

    case types.GET_QUESTIONS_SUCCESS:
      return {
        ...state,
        questions: action.payload,
        isGetQuestions: true,
      };

    case types.GET_QUESTIONS_ERROR:
      return {
        ...state,
        isGetQuestions: false,
      };
    case types.GET_QUESTION_BY_ID:
      return {
        ...state,
        isGetQuestionById: false,
      };

    case types.GET_QUESTION_BY_ID_SUCCESS:
      return {
        ...state,
        questionById: action.payload,
        isGetQuestionById: true,
      };

    case types.GET_QUESTION_BY_ID_ERROR:
      return {
        ...state,
        isGetQuestionById: false,
      };
    case types.UPDATE_QUESTION:
      return {
        ...state,
        isQuestionUpdated: false,
      };

    case types.UPDATE_QUESTION_SUCCESS:
      return {
        ...state,
        isQuestionUpdated: true,
      };

    case types.UPDATE_QUESTION_ERROR:
      return {
        ...state,
        isQuestionUpdated: false,
      };

    case types.DELETE_QUESTION:
      return {
        ...state,
        isQuestionDeleted: false,
      };

    case types.DELETE_QUESTION_SUCCESS:
      return {
        ...state,
        isQuestionDeleted: true,
      };

    case types.DELETE_QUESTION_ERROR:
      return {
        ...state,
        isQuestionDeleted: false,
      };
    case types.GET_TEST_BY_ID:
      return {
        ...state,
        isGetTestById: false,
      };

    case types.GET_TEST_BY_ID_SUCCESS:
      return {
        ...state,
        testById: action.payload,
        isGetTestById: true,
      };

    case types.GET_TEST_BY_ID_ERROR:
      return {
        ...state,
        isGetTestById: false,
      };
    case types.UPDATE_TEST:
      return {
        ...state,
        isTestUpdated: false,
      };

    case types.UPDATE_TEST_SUCCESS:
      return {
        ...state,
        isTestUpdated: true,
      };

    case types.UPDATE_TEST_ERROR:
      return {
        ...state,
        isTestUpdated: false,
      };

    case types.DELETE_TEST:
      return {
        ...state,
        isTestDeleted: false,
      };

    case types.DELETE_TEST_SUCCESS:
      return {
        ...state,
        isTestDeleted: true,
      };

    case types.DELETE_TEST_ERROR:
      return {
        ...state,
        isTestDeleted: false,
      };

    case types.GET_ALL_TEST:
      return {
        ...state,
        isGetAllTest: false,
      };

    case types.GET_ALL_TEST_SUCCESS:
      return {
        ...state,
        allTest: action.payload,
        isGetAllTest: true,
      };

    case types.GET_ALL_TEST_ERROR:
      return {
        ...state,
        isGetAllTest: false,
      };

    // case types.GET_ALL_SKILLS:
    //   return {
    //     ...state,
    //     isGetAllSkills: false,
    //   };

    // case types.GET_ALL_SKILLS_SUCCESS:
    //   return {
    //     ...state,
    //     allSkills: action.payload,
    //     isGetAllSkills: true,
    //   };

    // case types.GET_ALL_SKILLS_ERROR:
      // return {
      //   ...state,
      //   isGetAllSkills: false,
      // };
      case types.GET_ALL_LANGUAGES:
        return {
          ...state,
          isGetAllLanguages: false,
        };
  
      case types.GET_ALL_LANGUAGES_SUCCESS:
        return {
          ...state,
          allLanguages: action.payload,
          isGetAllLanguages: true,
        };
  
      case types.GET_ALL_LANGUAGES_ERROR:
        return {
          ...state,
          isGetAllLanguages: false,
        };






    default:
      return state;
  }
}
