import * as types from "./QuestionTypes";

const INITIAL_STATE = {
  isLoggedIn: false,
  questionData: [],
  isQuestionAdded: false,
  getQuestionData: [],
  isGetQuestionData: false,
  isQuestionDataUpdated: false,
  isQuestionDeleted: false,
};

export function QuestionReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case types.CREATE_QUESTION:
      return {
        ...state,
        isQuestionAdded: false,
      };

    case types.CREATE_QUESTION_SUCCESS:
      return {
        ...state,
        isQuestionAdded: true,
        questionData: action.payload,
      };

    case types.CREATE_QUESTION_ERROR:
      return {
        ...state,
        isQuestionAdded: false,
      };

    case types.GET_QUESTION:
      return {
        ...state,
        isGetQuestionData: false,
      };

    case types.GET_QUESTION_SUCCESS:
      return {
        ...state,
        getQuestionData: action.payload,
        isGetQuestionData: true,
      };

    case types.GET_QUESTION_ERROR:
      return {
        ...state,
        isGetQuestionData: false,
      };

    case types.UPDATE_QUESTION:
      return {
        ...state,
        isQuestionDataUpdated: false,
      };

    case types.UPDATE_QUESTION_SUCCESS:
      return {
        ...state,
        isQuestionDataUpdated: true,
      };

    case types.UPDATE_QUESTION_ERROR:
      return {
        ...state,
        isQuestionDataUpdated: false,
      };

    case types.DELETE_QUESTION:
      return {
        ...state,
        isQuestionDeleted: false,
      };

    case types.DELETE_QUESTION_SUCCESS:
      return {
        ...state,
        isQuestionDeleted: true,
      };

    case types.DELETE_QUESTION_ERROR:
      return {
        ...state,
        isQuestionDeleted: false,
      };

    default:
      return state;
  }
}
