import * as types from "./JobDataTypes";

const INITIAL_STATE = {
  jobData: [],
  isJobDataAdded: false,
  getJobData: [],
  isGetJobData: false,
  isJobDataUpdated: false,
  isJobDataDeleted: false,
  isJobDataById: false,
  getJobDataById: [],
  skillData: [],
  jobDataAdded:false
};

export function JobDataReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case types.GET_SKILLS_SUCCESS:
      return {
        ...state,
        skillData: action.payload,
      };
    case types.CREATE_JOBDATA:
      return {
        ...state,
        isJobDataAdded: false,
        jobDataAdded:false
      };

    case types.CREATE_JOBDATA_SUCCESS:
      return {
        ...state,
        isJobDataAdded: true,
        jobData: action.payload,
        jobDataAdded:true
      };

    case types.CREATE_JOBDATA_ERROR:
      return {
        ...state,
        isJobDataAdded: false,
        jobDataAdded:false
      };
    case types.GET_JOBDATA:
      return {
        ...state,
        isGetJobData: false,
      };

    case types.GET_JOBDATA_SUCCESS:
      return {
        ...state,
        getJobData: action.payload,
        isGetJobData: true,
      };

    case types.GET_JOBDATA_ERROR:
      return {
        ...state,
        isGetJobData: false,
      };

    default:
      return state;
  }
}
