import * as types from "./UserTypes";
const INITIAL_STATE = {
  CurrentUser: [],
  isLoggedIn: false,
  isGetUserData: false,
  getUserData: [],
  isUserDataAdded: false,
  isDeletedUserData: false,
  isUserDataUpdated: false,
  updatePassword: [],
  isUpdatePassword: false,
  getSubscription: [],
  isGetSubscription: false,
  refreshToken:[],
  isRefreshTokenPosted:false,
  isLoggedOut:false,
  loaderValue:0,
  stop:true
};

export function UserReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case types.LOGIN:
      let isLoggedIn = action.payload.status === 200 ? true : false;
      return {
        ...state,
        isLoggedIn: isLoggedIn,
      };
    case types.LOGIN_SUCCESS:
      let isLoggedIn1 = action.payload.status === 200 ? true : false;
      return {
        ...state,
        CurrentUser: action.payload.data,
        isLoggedIn: isLoggedIn1,
        isLoggedOut:false
      };

      case types.POST_REFRESH_TOKEN:
        return {
          ...state,
          isRefreshTokenPosted: false,
        };
  
      case types.POST_REFRESH_TOKEN_SUCCESS:
        return {
          ...state,
          isRefreshTokenPosted: true,
          refreshToken: action.payload,
        };
  
      case types.POST_REFRESH_TOKEN_ERROR:
        return {
          ...state,
          isRefreshTokenPosted: false,
        };

    case types.LOGOUT:
      return {
        ...state,
        isLoggedOut:false
      };
      case types.LOGOUT_SUCCESS:
        return {
          ...state,
          isLoggedOut:true
        };
        case types.LOGOUT_ERROR:
          return {
            ...state,
            isLoggedOut:false
          };

    case types.GET_USER:
      return {
        ...state,
        isGetUserData: false,
      };

    case types.GET_USER_SUCCESS:
      return {
        ...state,
        getUserData: action.payload,
        isGetUserData: true,
      };

    case types.GET_USER_ERROR:
      return {
        ...state,
        isGetUserData: false,
      };

    case types.CREATE_USER:
      return {
        ...state,
        isUserDataAdded: false,
      };

    case types.CREATE_USER_SUCCESS:
      return {
        ...state,
        isUserDataAdded: true,
      };

    case types.CREATE_USER_ERROR:
      return {
        ...state,
        isUserDataAdded: false,
      };

    case types.DELETE_USER_DATA:
      return {
        ...state,
        isDeletedUserData: false,
      };

    case types.DELETE_USER_DATA_SUCCESS:
      return {
        ...state,
        isDeletedUserData: true,
      };

    case types.DELETE_USER_DATA_ERROR:
      return {
        ...state,
        isDeletedUserData: false,
      };
    case types.UPDATE_USER:
      return {
        ...state,
        isUserDataUpdated: false,
      };

    case types.UPDATE_USER_SUCCESS:
      return {
        ...state,
        isUserDataUpdated: true,
      };

    case types.UPDATE_USER_ERROR:
      return {
        ...state,
        isUserDataUpdated: false,
      };

    case types.UPDATE_PASSWORD:
      return {
        ...state,
        isUpdatePassword: false,
        updatePassword: [],
      };

    case types.UPDATE_PASSWORD_SUCCESS:
      return {
        ...state,
        isUpdatePassword: true,
        updatePassword: action.payload,
      };

    case types.UPDATE_PASSWORD_ERROR:
      return {
        ...state,
        isUpdatePassword: false,
        updatePassword: [],
      };

    case types.GET_SUBSCRIPTION:
      return {
        ...state,
        isGetSubscription: false,
        getSubscription: [],
      };

    case types.GET_SUBSCRIPTION_SUCCESS:
      return {
        ...state,
        isGetSubscription: true,
        getSubscription: action.payload,
      };

    case types.GET_SUBSCRIPTION_ERROR:
      return {
        ...state,
        isGetSubscription: false,
        getSubscription: [],
      };

      case types.INCREMENT_LOADER:
        return {
          ...state,
         loaderValue:state.loaderValue+1,
         stop:false
        };
        case types.DECREMENT_LOADER:
          if(state.loaderValue==1){
            return {
              ...state,
             loaderValue:state.loaderValue-1,
             stop:true
         
            }
          }else if(state.loaderValue>1){
            return {
              ...state,
             loaderValue:state.loaderValue-1,
         
            }
          }
        

    default:
      return state;
  }
}
