import * as types from "./DashboardTypes";

export function getDashboardData(payload) {
  return {
    type: types.GET_DASHBOARD,
    payload: payload,
  };
}

export function getDashboardDataSuccess(payload) {
  return {
    type: types.GET_DASHBOARD_SUCCESS,
    payload: payload,
  };
}

export function getDashboardDataError(error) {
  return {
    type: types.GET_DASHBOARD_ERROR,
    payload: error,
  };
}
export function dateFilterDashboard(payload) {
  return {
    type: types.DATE_FILTER_DASHBOARD,
    payload: payload,
  };
}

export function dateFilterDashboardSuccess(payload) {
  return {
    type: types.DATE_FILTER_DASHBOARD_SUCCESS,
    payload: payload,
  };
}

export function dateFilterDashboardError(error) {
  return {
    type: types.DATE_FILTER_DASHBOARD_ERROR,
    payload: error,
  };
}
