import React, { Component } from "react";
import CloseIcon from "@material-ui/icons/Close";
import "../Skills/skillBox.css";

class Email extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <div className="card">
        <div className="row">
          {this.props.emailArr.map((item, index) => {
            return (
              <div className="items" key={item.id}>
                <div
                  onClick={() => this.props.removeEmail(item.id)}
                  className="icon"
                >
                  <CloseIcon style={{ fontSize: "medium" }} />
                </div>
                &nbsp;&nbsp;
                <div className="text">{item.email}</div>
              </div>
            );
          })}
        </div>
      </div>
    );
  }
}

export default Email;

