import * as types from "./QuestionCreateTypes";

const INITIAL_STATE = {
  isLoggedIn: false,
  questionData: [],
  isQuestionAdded: false,
  getQuestionData: [],
  isGetQuestionData: false,
  isQuestionDataUpdated: false,
  isQuestionDeleted: false,
  isgetquestionByIdData: false,
  getquestionByIdData: [],
};

export function QuestionCreateReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case types.CREATE_NEWQUESTION:
      return {
        ...state,
        isQuestionAdded: false,
      };

    case types.CREATE_NEWQUESTION_SUCCESS:
      return {
        ...state,
        isQuestionAdded: true,
        questionData: action.payload,
      };

    case types.CREATE_NEWQUESTION_ERROR:
      return {
        ...state,
        isQuestionAdded: false,
      };

    case types.GET_NEWQUESTION:
      return {
        ...state,
        isGetQuestionData: false,
      };

    case types.GET_NEWQUESTION_SUCCESS:
      return {
        ...state,
        getQuestionData: action.payload,
        isGetQuestionData: true,
      };

    case types.GET_NEWQUESTION_ERROR:
      return {
        ...state,
        isGetQuestionData: false,
      };

    case types.UPDATE_NEWQUESTION:
      return {
        ...state,
        isQuestionDataUpdated: false,
      };

    case types.UPDATE_NEWQUESTION_SUCCESS:
      return {
        ...state,
        isQuestionDataUpdated: true,
      };

    case types.UPDATE_NEWQUESTION_ERROR:
      return {
        ...state,
        isQuestionDataUpdated: false,
      };

    case types.DELETE_NEWQUESTION:
      return {
        ...state,
        isQuestionDeleted: false,
      };

    case types.DELETE_NEWQUESTION_SUCCESS:
      return {
        ...state,
        isQuestionDeleted: true,
      };

    case types.DELETE_NEWQUESTION_ERROR:
      return {
        ...state,
        isQuestionDeleted: false,
      };
    case types.GET_QUESTION_BY_ID:
      return {
        ...state,
        isgetquestionByIdData: false,
        getquestionByIdData: [],
      };

    case types.GET_QUESTION_BY_ID_SUCCESS:
      return {
        ...state,
        isgetquestionByIdData: true,
        getquestionByIdData: action.payload,
      };

    case types.GET_QUESTION_BY_ID_ERROR:
      return {
        ...state,
        isgetquestionByIdData: false,
        getquestionByIdData: [],
      };

    default:
      return state;
  }
}
