import * as types from "./PlandataTypes";

export function addPlanData(payload) {
  return {
    type: types.CREATE_PLAN,
    payload: payload,
  };
}
export function addPlanDataSuccess(payload) {
  return {
    type: types.CREATE_PLAN_SUCCESS,
    payload: payload,
  };
}
export function addPlanDataError(error) {
  return {
    type: types.CREATE_PLAN_ERROR,
    payload: error,
  };
}

export function getPlanData(payload) {
  return {
    type: types.GET_PLAN,
    payload: payload,
  };
}
export function getPlanDataSuccess(payload) {
  return {
    type: types.GET_PLAN_SUCCESS,
    payload: payload,
  };
}
export function getPlanDataError(error) {
  return {
    type: types.GET_PLAN_ERROR,
    payload: error,
  };
}
