import { call, put } from "redux-saga/effects";
import * as ACTIONS from "./resumesourceAction";
import { takeLatest } from "redux-saga/effects";
import * as TYPES from "./resumesourceTypes";
import apiJunction from "../../utils/api";
import serverAddress from "../../config";
import { store } from "react-notifications-component";
const candid = localStorage.getItem("id");

export function* addCandidateData(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "post",
      url: `${serverAddress}/api/rs/create`,
      body: action.payload,
    });
    if (result) {
      yield put(ACTIONS.addCandidateDataSuccess(result));
      store.addNotification({
        title: "Success",
        message: "Candidate created successfully!!",
        type: "success",
        container: "top-center",
        animationIn: ["animated", "fadeIn"],
        animationOut: ["animated", "fadeOut"],
        dismiss: {
          duration: 2000,
        },
      });
    }
  } catch (e) {
    yield put(ACTIONS.addCandidateDataError(e.response));
    store.addNotification({
      title: "Error",
      message: "Fill All Data and submit",
      type: "danger",
      container: "top-center",
      animationIn: ["animated", "fadeIn"],
      animationOut: ["animated", "fadeOut"],
      dismiss: {
        duration: 2000,
      },
    });
  }
}
export function* submitCandidateData(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "post",
      url: `${serverAddress}/candidate/bulk`,
      body: action.payload,
    });
    if (result && result.status==201) {
      yield put(ACTIONS.submitCandidateDataSuccess(result));
      store.addNotification({
        title: "Success",
        message: "Candidate created successfully!!",
        type: "success",
        container: "top-center",
        animationIn: ["animated", "fadeIn"],
        animationOut: ["animated", "fadeOut"],
        dismiss: {
          duration: 2000,
        },
      });
    }else{
      store.addNotification({
        title: "Error",
        message: "Failed to add candidates",
        type: "danger",
        container: "top-center",
        animationIn: ["animated", "fadeIn"],
        animationOut: ["animated", "fadeOut"],
        dismiss: {
          duration: 2000,
        },
      });
    }
  } catch (e) {
    yield put(ACTIONS.submitCandidateDataError(e.response));
    store.addNotification({
      title: "Error",
      message: "Failed to add candidates",
      type: "danger",
      container: "top-center",
      animationIn: ["animated", "fadeIn"],
      animationOut: ["animated", "fadeOut"],
      dismiss: {
        duration: 2000,
      },
    });
  }
}

export function* getCandidateData(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "get",
      url: `${serverAddress}/api/rs/get/${candid}?page=${action.payload.page}&limit=${action.payload.limit}`,
    });
    if (result) {
      yield put(ACTIONS.getCandidateDataSuccess(result));
    }
  } catch (e) {
    yield put(ACTIONS.getCandidateDataError(e.response));
  }
}
export function* searchCandidateData(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "post",
      url: `${serverAddress}/api/rs/search?id=${action.payload.id}&name=${action.payload.name}`,
    });
    if (result) {
      yield put(ACTIONS.searchCandidateDataSuccess(result));
    }
  } catch (e) {
    yield put(ACTIONS.searchCandidateDataError(e.response));
    store.addNotification({
      title: "Failed",
      message: "Failed to add JD",
      type: "danger",
      container: "top-center",
      animationIn: ["animated", "fadeIn"],
      animationOut: ["animated", "fadeOut"],
      dismiss: {
        duration: 2000,
      },
    });
  }
}

export function* deleteCandidateData(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "delete",
      url: `${serverAddress}/candidate/${action.payload.id}`,
    });

    if (result) {
      yield put(ACTIONS.deleteCandidateDataSuccess(result));
      store.addNotification({
        title: "Success",
        message: "Candidate Deleted successfully!!",
        type: "success",
        container: "top-center",
        animationIn: ["animated", "fadeIn"],
        animationOut: ["animated", "fadeOut"],
        dismiss: {
          duration: 2000,
        },
      });
    }
  } catch (e) {
    yield put(ACTIONS.deleteCandidateDataError(e.response));
    store.addNotification({
      title: "Error",
      message: e.response,
      type: "danger",
      container: "top-center",
      animationIn: ["animated", "fadeIn"],
      animationOut: ["animated", "fadeOut"],
      dismiss: {
        duration: 2000,
      },
    });
  }
}
export function* getCandidateByIdData(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "get",
      url: `${serverAddress}/candidate?page=${action.payload.page}&limit=${
        action.payload.limit
      }${action.payload.name ? "&name=" + action.payload.name : ""}${
        action.payload.jdId ? "&jdId=" + action.payload.jdId : ""
      }${action.payload.status ? "&status=" + action.payload.status : ""}`,
    });

    yield put(ACTIONS.getCandidateByIdSuccess(result));
  } catch (e) {
    store.addNotification({
      title: "Error",
      message: e.response,
      type: "danger",
      container: "top-center",
      animationIn: ["animated", "fadeIn"],
      animationOut: ["animated", "fadeOut"],
      dismiss: {
        duration: 2000,
      },
    });

    yield put(ACTIONS.getCandidateByIdError(e.response));
  }
}

export function* updateCandidateData(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "patch",
      url: `${serverAddress}/candidate/${action.payload.id}`,
      body: action.payload.body,
    });
    if (result && result.status == 200) {
      yield put(ACTIONS.updateCandidateSuccess(result));
      store.addNotification({
        title: "Success",
        message: "Status Updated successfully",
        type: "success",
        container: "top-center",
        animationIn: ["animated", "fadeIn"],
        animationOut: ["animated", "fadeOut"],
        dismiss: {
          duration: 2000,
        },
      });
    }else{
      store.addNotification({
        title: "Error",
        message: "Status not Updated",
        type: "danger",
        container: "top-center",
        animationIn: ["animated", "fadeIn"],
        animationOut: ["animated", "fadeOut"],
        dismiss: {
          duration: 2000,
        },
      });
    }
  } catch (e) {
    yield put(ACTIONS.updateCandidateError(e.response));

    store.addNotification({
      title: "Error",
      message: "Status not Updated",
      type: "danger",
      container: "top-center",
      animationIn: ["animated", "fadeIn"],
      animationOut: ["animated", "fadeOut"],
      dismiss: {
        duration: 2000,
      },
    });
  }
}

export function* filterByDate(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "post",
      url: `${serverAddress}/api/rs/filterdate`,
      body: action.payload,
    });
    if (result) {
      yield put(ACTIONS.filterByDateSuccess(result));
      store.addNotification({
        title: "Success",
        message: "Candidate filtered successfully!!",
        type: "success",
        container: "top-center",
        animationIn: ["animated", "fadeIn"],
        animationOut: ["animated", "fadeOut"],
        dismiss: {
          duration: 2000,
        },
      });
    }
  } catch (e) {
    yield put(ACTIONS.filterByDateError(e.response));
    store.addNotification({
      title: "Failed",
      message: "Failed to filter Candidate",
      type: "danger",
      container: "top-center",
      animationIn: ["animated", "fadeIn"],
      animationOut: ["animated", "fadeOut"],
      dismiss: {
        duration: 2000,
      },
    });
  }
}

export function* ResumesourceSaga() {
  yield takeLatest(TYPES.CREATE_CANDIDATE, addCandidateData);
  yield takeLatest(TYPES.GET_CANDIDATE, getCandidateData);
  yield takeLatest(TYPES.SEARCH_CANDIDATE, getCandidateData);
  yield takeLatest(TYPES.DELETE_CANDIDATE, deleteCandidateData);
  yield takeLatest(TYPES.SEARCH_CANDIDATE, searchCandidateData);
  yield takeLatest(TYPES.GET_CANDIDATE_BY_ID, getCandidateByIdData);
  yield takeLatest(TYPES.UPDATE_CANDIDATE, updateCandidateData);
  yield takeLatest(TYPES.FILTER_BY_DATE, filterByDate);
  yield takeLatest(TYPES.SUBMIT_CANDIDATE, submitCandidateData);
}
