import { call, put } from "redux-saga/effects";
import * as ACTIONS from "./JobDataAction";
import { takeLatest } from "redux-saga/effects";
import * as TYPES from "./JobDataTypes";
import apiJunction from "../../utils/api";
import serverAddress from "../../config";
import { store } from "react-notifications-component";
import history from "../../routes/History";
const candid = localStorage.getItem("id");
export function* getSkillS(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "get",
      url: `${serverAddress}/jd/skills`,
    });
    if (result) {
      yield put(ACTIONS.getSkillSDataSuccess(result));
    }
  } catch (e) {
    yield put(ACTIONS.getSkillSDataError(e.response));
  }
}
export function* addJobData(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "post",
      url: `${serverAddress}/api/jobtitle/create`,
      body: action.payload,
    });
    if (result) {
      yield put(ACTIONS.addJobDataSuccess(result && result.data.jobTitle));
      const candid = localStorage.getItem("id");
      const payload1 = {
        UserId: candid,
      };
      yield put(ACTIONS.getJobData(payload1));
      store.addNotification({
        title: "Success",
        message: "Job Title added successfully!!",
        type: "success",
        container: "bottom-center",
        animationIn: ["animated", "fadeIn"],
        animationOut: ["animated", "fadeOut"],
        dismiss: {
          duration: 2000,
        },
      });
    }
  } catch (e) {
    yield put(ACTIONS.addJobDataError(e.response));
    store.addNotification({
      title: "Failed",
      message: "Failed to add Job Title",
      type: "danger",
      container: "bottom-center",
      animationIn: ["animated", "fadeIn"],
      animationOut: ["animated", "fadeOut"],
      dismiss: {
        duration: 2000,
      },
    });
  }
}

export function* getJobData() {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "get",
      url: `${serverAddress}/api/jobtitle/get/${candid}`,
    });
    if (result) {
      yield put(ACTIONS.getJobDataSuccess(result));
    }
  } catch (e) {
    yield put(ACTIONS.getJobDataError(e.response));
  }
}

export function* JobDataSaga() {
  yield takeLatest(TYPES.CREATE_JOBDATA, addJobData);
  yield takeLatest(TYPES.GET_JOBDATA, getJobData);
  yield takeLatest(TYPES.GET_SKILLS, getSkillS);
}
