import { call, put } from "redux-saga/effects";
import * as ACTIONS from "./DashboardAction";
import { takeLatest } from "redux-saga/effects";
import * as TYPES from "./DashboardTypes";
import apiJunction from "../../utils/api";
import serverAddress from "../../config";
import { store } from "react-notifications-component";
import history from "../../routes/History";
const candid = localStorage.getItem("id");

export function* getDashboardData(action) {
  try {
    const { startDate, endDate, jdId } = action.payload;
    const result = yield call(apiJunction.makeRequest, {
      method: "get",
      url: `${serverAddress}/dashboard/?${
        startDate ? "startDate=" + startDate : ""
      }${endDate ? "&endDate=" + endDate : ""}${jdId ? "&jdId=" + jdId : ""}`,
    });
    if (result) {
      yield put(ACTIONS.getDashboardDataSuccess(result));
    }
  } catch (e) {
    yield put(ACTIONS.getDashboardDataError(e.response));
  }
}

export function* dateFilterDashboard(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "post",
      url: `${serverAddress}/api/dashboard/getDatefilteredDasboard`,
      body: action.payload,
    });

    if (result) {
      yield put(ACTIONS.dateFilterDashboardSuccess(result));
      store.addNotification({
        title: "Success",
        message: "Dashboard filtered successfully!!",
        type: "success",
        container: "top-center",
        animationIn: ["animated", "fadeIn"],
        animationOut: ["animated", "fadeOut"],
        dismiss: {
          duration: 2000,
        },
      });
    }
  } catch (e) {
    yield put(ACTIONS.dateFilterDashboardError(e.response));
    store.addNotification({
      title: "Failed",
      message: "Failed to add Dashboard",
      type: "danger",
      container: "top-center",
      animationIn: ["animated", "fadeIn"],
      animationOut: ["animated", "fadeOut"],
      dismiss: {
        duration: 2000,
      },
    });
  }
}

export function* DashboardSaga() {
  yield takeLatest(TYPES.GET_DASHBOARD, getDashboardData);
  yield takeLatest(TYPES.DATE_FILTER_DASHBOARD, dateFilterDashboard);
}
