import React from "react";
import Navbar from "../components/Navbar/Navbar";
import { Router } from "react-router-dom";
import history from "../routes/History";
import Routes from "../routes/Routes";
import SideNav from "../sideNav/sideNav";
import Loader from "../components/Loader/Loader";
import "./App.scss";
import "react-notifications-component/dist/theme.css";
import { connect } from "react-redux";
import * as Actions from "../store/User/UserAction";
import { bindActionCreators } from "redux";
import LoadingBar from "react-top-loading-bar";
import { withRouter } from "react-router-dom";
import * as AssessmentAction from "../store/Assessment/AssessmentAction";
import ReactGA from "react-ga";

ReactGA.initialize("UA-58884491-1");
class App extends React.Component {
  state = {
    navClass: false,
    progress: 0,
    stop: true,
  };
  // App contains routes and also wrapped with snackbar and intl for localization
  toggleClass = () => {
    this.setState({ navClass: !this.state.navClass }, () => {});
  };

  componentDidUpdate = (prevProps) => {
    const prev = prevProps && prevProps.UserReducer;
    const cur = this.props && this.props.UserReducer;

    if (prev.stop != cur.stop) {
      if (cur.stop == true) {
        this.setState({
          progress: 100,
        });
        setTimeout(() => {
          this.setState({
            stop: true,
          });
        }, 1000);
      } else {
        this.setState({
          stop: cur.stop,
        });
      }
    }

    const prevNew = prevProps && prevProps.AssessmentReducer;
    const curNew = this.props && this.props.AssessmentReducer;

    if (prevNew.isGetQuestionTypes !== curNew.isGetQuestionTypes) {
      if (
        curNew.questionType &&
        curNew.questionType.data &&
        curNew.questionType.data.results &&
        curNew.questionType.data.results.length
      ) {
        curNew.questionType.data.results.map((item) => {
          if (
            item.name.toLowerCase() == "mcq" &&
            !localStorage.getItem("mcq")
          ) {
            localStorage.setItem("mcq", item.id);
          }
          if (
            item.name.toLowerCase() == "verbal" &&
            !localStorage.getItem("verbal")
          ) {
            localStorage.setItem("verbal", item.id);
          }
          if (
            item.name.toLowerCase() == "coding" &&
            !localStorage.getItem("coding")
          ) {
            localStorage.setItem("coding", item.id);
          }
        });
      }
    }
  };

  componentDidMount = () => {
    if (
      !localStorage.getItem("mcq") ||
      !localStorage.getItem("verbal") ||
      !localStorage.getItem("coding")
    ) {
      this.props.AssessmentAction.getQuestionTypes();
    }
    setInterval(() => this.handleProgressChange(), 100);
    this.loadData();
    let interval = 1000 * 60 * 15; // in milli seconds
    setInterval(this.loadData, interval);
    if (history.location.pathname === "/") {
      history.push("/login");
    }

    if (
      !(
        (localStorage.getItem("isLoggedIn") && localStorage.getItem("token")) ||
        history.location.pathname === "/login" ||
        history.location.pathname.includes("/response/share-interview") ||
        history.location.pathname.includes("/response/share-multi-interview")
      )
    ) {
      history.push("/login");
    }
    if (
      localStorage.getItem("isLoggedIn") &&
      localStorage.getItem("token") &&
      (history.location.pathname === "/login" ||
        history.location.pathname === "/")
    ) {
      history.push("/dashboard");
    }
  };

  handleProgressChange = () => {
    if (this.state.progress < 90 && !this.state.stop) {
      this.setState({
        progress: this.state.progress + 15,
      });
    }
  };

  loadData = () => {
    let difference = 0;

    if (
      localStorage.getItem("token_expiry_time") &&
      localStorage.getItem("refresh_token") &&
      new Date(localStorage.getItem("token_expiry_time")) > new Date()
    ) {
      difference =
        Math.abs(
          new Date().getTime() -
            new Date(localStorage.getItem("token_expiry_time")).getTime()
        ) /
        (1000 * 60); //minutes

      let expecptedDifference = 10; // in minutes
      if (difference <= expecptedDifference) {
        this.props.UserAction.postRefreshToken({
          refreshToken: localStorage.getItem("refresh_token"),
        });
      }
    } else {
      if (
        localStorage.getItem("refresh_token") &&
        new Date(localStorage.getItem("token_expiry_time")) < new Date()
      ) {
        this.props.UserAction.postRefreshToken({
          refreshToken: localStorage.getItem("refresh_token"),
        });
      } else {
        if (
          !(
            window.location.pathname === "/login" ||
            window.location.pathname.includes("/response/share-interview") ||
            window.location.pathname.includes("/response/share-multi-interview")
          )
        ) {
          this.props.history.push("/login");
        }
      }
    }
  };

  render() {
    const { pathname } = window.location;
    const { loading } = this.props;
    let showSideNav = false;
    let showHeader = false;

    if (pathname !== "/login") {
      showSideNav = true;
      showHeader = true;
    }

    if (
      pathname === "/" ||
      pathname === "/login" ||
      pathname === "/dummy" ||
      pathname.includes("/response/share-interview") ||
      pathname.includes("/response/share-multi-interview")
    ) {
      showSideNav = false;
      showHeader = false;
    }

    if (pathname.includes("/invites") || pathname.includes("/history/")) {
      showHeader = false;
    }
    return (
      <div>
        {!this.state.stop && pathname != "/login" && pathname != "/dummy" ? (
          <LoadingBar
            color='#04724d'
            height={5}
            progress={this.state.progress}
            onLoaderFinished={() => {
              this.setState({ progress: 0 });
            }}
          />
        ) : null}
        {loading ? <Loader /> : null}

        <Router history={history}>
          <div className='mainPage'>
            {showSideNav ? (
              <div>
                <SideNav
                  {...this.props}
                  toggleClass={this.toggleClass}
                  navClass={this.state.navClass}
                />
              </div>
            ) : null}

            <div
              id={
                showSideNav && showHeader
                  ? !this.state.navClass
                    ? "content"
                    : "content-toggle"
                  : pathname.includes("/invites") ||
                    pathname.includes("/history/")
                  ? "content"
                  : "login"
              }
            >
              {showHeader ? <Navbar /> : null}
              <Routes />
            </div>
          </div>
        </Router>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  UserReducer: state.UserReducer,
  AssessmentReducer: state.AssessmentReducer,
});
const mapDispatchToProps = (dispatch) => {
  return {
    UserAction: bindActionCreators(Actions, dispatch),
    AssessmentAction: bindActionCreators(AssessmentAction, dispatch),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(App));
